<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <span  class="titlePages">{{title}}</span>
        <v-row class="ma-0 mt-8" >

            <v-sheet color="transparent" :style="mdAndDown ?'width: 100%' :`width: calc(100% - ${width}px)`" >   
                <div id="map"></div>
            </v-sheet>

            
            <!-- <v-col class="pa-0" v-if="showFilters" cols="12" sm="12" md="12" lg="2" xl="2" xxl="2">                     -->
                <v-sheet color="transparent" class="pa-4 pr-0" :width="width">
                    <h3  class="subtitlePages">Heatmap</h3>
                    <v-row class="ma-0 mt-3 mb-3">
                        <v-col cols="6" class="pa-0 pr-2"> <v-text-field variant="outlined" density="compact" label="Duración (min.)" type="number" hide-spin-buttons v-model="minutes"/> </v-col>
                        <v-col cols="6" class="pa-0 pl-2"> <v-text-field variant="outlined" density="compact" label="Intervalo (seg.)" type="number" hide-spin-buttons v-model="interval"/> </v-col>
                        <v-btn @click="startHeatMap" block color="success" :disabled="disabledAXIOS"> Iniciar </v-btn>
                    </v-row>
                    <v-row v-if="disabledAXIOS" class="ma-0 mt-2 mb-4" justify="center">
                        Cargando Información 
                        <v-progress-linear
                        :model-value="progress"
                        height="20"
                        color="green"
                        striped
                        :active="isRunning"
                        class="mt-4"
                        >
                        <template #default>
                            {{ Math.floor(progress) }}%
                        </template>
                        </v-progress-linear>
                    </v-row>
                    <v-row class="ma-0">
                        <v-col cols="12" class="pa-0">
                            <h3> Búsqueda </h3>
                            <v-select
                            v-model="haulTruckSelected"
                            :items="computedListTruck"
                            :disabled="computedListTruck.length == 0 && disabledAXIOS"
                            item-title="title"
                            item-value="value"
                            density="compact"
                            variant="outlined"
                            hide-details
                            />
                            <v-btn class='mt-4' color="success" :disabled="haulTruckSelected===''" block @click="getHaulTruckPositions">{{  t('buttons.searchTruck') }}</v-btn>
                        </v-col>
                    </v-row>
                    <v-row v-if="showError">
                        <v-col>No hay informacion</v-col>
                    </v-row>
                    <v-row class="ma-0" v-if="showCheckBoxes">
                        <v-col class="pa-0" cols="12">
                            <v-checkbox
                            v-model="selectedETH0"
                            color="green"
                            label="ETH0"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-model="selectedRPTPEER"
                            color="green"
                            label="RPTPEER"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-if="finalWLAN0Array.length > 0"
                            v-model="selectedWLAN0"
                            color="green"
                            label="WLAN0"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-if="finalWLAN1Array.length > 0"
                            v-model="selectedWLAN1"
                            color="green"
                            label="WLAN1"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-if="finalWLAN2Array.length > 0"
                            v-model="selectedWLAN2"
                            color="green"
                            label="WLAN2"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-if="finalWLAN3Array.length > 0"
                            v-model="selectedWLAN3"
                            color="green"
                            label="WLAN3"
                            hide-details
                            ></v-checkbox>
                            <v-checkbox
                            v-if="finalLATENCIAArray.length > 0"
                            v-model="selectedLATENCIA"
                            color="green"
                            label="LATENCIA"
                            hide-details
                            ></v-checkbox>
                        </v-col>
                        <v-col cols="12">
                            <v-btn color="red" block @click=getKMZ()>
                                Generar KMZ
                            </v-btn>
                        </v-col>
                        
                    </v-row>
                </v-sheet>
            <!-- </v-col> -->
        </v-row>
    </v-sheet>
</template>

<script setup lang="ts">
import { watch, ref, Ref, onMounted, ComputedRef, computed} from 'vue';
import axios            from 'axios';
import { useDisplay }   from 'vuetify';
import { useI18n }      from 'vue-i18n';
import Papa from "papaparse";

import maplibregl, { LngLatLike } from 'maplibre-gl';
import 'maplibre-gl/dist/maplibre-gl.css';

//CONSTANTS
import CONSTAPIS        from '@/constants/apis';
import { COLOR }      from '@/constants/colors';
import { LABELS }       from '@/constants/labels';

// COMPONENTS

// INTERFACES
import { STRINGS }          from '@/interfaces/SmartLink/General';
import { TOPO_ITEMS }       from '@/interfaces/SmartLink/Topology';
import { showConsoleError } from '@/functions/errors';
import { DRIVE_OBS, DRIVE_PROPS } from '@/interfaces/SmartLink/Equipos';

        
import { useStore }         from 'vuex';
const store         = useStore();

const { t }                                 = useI18n();
const { mdAndDown }                         = useDisplay();
const width                                 = ref('350');

watch(mdAndDown,(valor:boolean) => {
    valor ? width.value = '100%' :  width.value = '350';
})


const TITLE_DRIVE:ComputedRef<STRINGS>      =  computed(() => {
    return {
        drive         : 'Drive',
        driverajant   : 'Drive Rajant',
    }
});

const minutes = ref(1)
const interval = ref(15)
const disabledAXIOS = ref(false)

async function startHeatMap(){
    const params = {
        minutes: minutes.value,
        interval: interval.value
    }

    disabledAXIOS.value = true;

    const allData = await postHeatMapRAJANT(params)

    disabledAXIOS.value = false

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        if(allData.data[0].includes('ERROR')){
            store.commit('showSnackBar', {type: LABELS.error, msg: allData.data[1], valor : true});
        }
        else{
            store.commit('showSnackBar', {type: LABELS.add, msg: t('snackbar.heatmapOK'), valor : true});
        }
    }
}

watch(disabledAXIOS,(a)=> {
    if(a) startTimer()
})


const progress = ref(0);
const isRunning = ref(false);

let totalSeconds = 0;
let elapsedSeconds = 0;
let intervalId: ReturnType<typeof setInterval> | null = null;

const startTimer = () => {
  if (!minutes.value || minutes.value <= 0) return;

  if (intervalId) {
    clearInterval(intervalId);
  }

  totalSeconds = minutes.value * 60;
  elapsedSeconds = 0;
  progress.value = 0;
  isRunning.value = true;

  intervalId = setInterval(() => {
    elapsedSeconds++;
    progress.value = (elapsedSeconds / totalSeconds) * 100;

    if (elapsedSeconds >= totalSeconds) {
      clearInterval(intervalId!);
      isRunning.value = false;
    }
  }, 1000);
};

/**
 * Get PMPAP Data
 */


const infoWindowVisible     = ref(false);

const getValueStoreDRIVE    = ref('driverajant');
const title                 = computed(() => TITLE_DRIVE.value[getValueStoreDRIVE.value]??'Title');

const showFilters           = ref(true);

const haulTruckSelected     = ref('');
const computedListTruck     = computed(() => finalListTruck.value)
const haulTrucksList:Ref<TOPO_ITEMS[]>      = ref([]);
const finalListTruck:Ref<TOPO_ITEMS[]>      = ref([]);

const showRangeSlider                   = ref(false);
const selectedETH0:Ref<boolean>         = ref(true);
const selectedRPTPEER:Ref<boolean>      = ref(false);
const selectedWLAN0:Ref<boolean>        = ref(false);
const selectedWLAN1:Ref<boolean>        = ref(false);
const selectedWLAN2:Ref<boolean>        = ref(false);
const selectedWLAN3:Ref<boolean>        = ref(false);
const selectedLATENCIA:Ref<boolean>     = ref(false);
const showError                         = ref(false);
const arrayDatesCircle:Ref<string[]>    = ref([]);
const showCheckBoxes                    = ref(false);

const COLORRED      = COLOR.red;
const COLORYELLOW   = COLOR.yellow;
const COLORGREEN    = COLOR.green;
const COLORGREY     = COLOR.grey;

const getColorByValue = (value: number, limit:number[], colors:string[]) => {
    if( value === 0 ) return COLORGREY;
    let finalColor = colors;
    let selectedColor = COLORGREY;
    limit.forEach((limited,i) => {
        if(value > limited ) selectedColor = finalColor[i]
    })
    return selectedColor
}

const finalETH0Array    = ref<DRIVE_PROPS[]>([]);
const finalETH0Object   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalRPTPEERArray    = ref<DRIVE_PROPS[]>([]);
const finalRPTPEERObject   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalWLAN0Array    = ref<DRIVE_PROPS[]>([]);
const finalWLAN0Object   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalWLAN1Array    = ref<DRIVE_PROPS[]>([]);
const finalWLAN1Object   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalWLAN2Array    = ref<DRIVE_PROPS[]>([]);
const finalWLAN2Object   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalWLAN3Array    = ref<DRIVE_PROPS[]>([]);
const finalWLAN3Object   = ref<DRIVE_OBS>({'features': [], "type": ''});

const finalLATENCIAArray    = ref<DRIVE_PROPS[]>([]);
const finalLATENCIAObject   = ref<DRIVE_OBS>({'features': [], "type": ''});


const getFinalPositions = (lat:number, lng:number) => {
    let lat1 = lat + 0.0002;
    let lng1 = lng + 0.0002;
    let finalItem = [
        [ lng,  lat  ],
        [ lng1, lat  ],
        [ lng1, lat1 ],
        [ lng,  lat1 ],
        [ lng,  lat  ],
    ]
    return finalItem
}

/**
 * Llama a la funcion getInfoByIP
 * Envia el parametro del valor que desea obtener
 * Recibe objeto {
 *  ip : name
 * }
 * se usa para la lista del SELECT
 */
// const finalNameByIP:Ref<STRINGS> = ref(getInfoByIP('name'));

const getAllHaulTrucks = async() => {

    showFilters.value = true;

    await axios.get(CONSTAPIS.APIS.value.drive.geAllIPDriveRAJANT, { timeout: 10000 }).then((response) => {
        let finalItems = response.data.body;
        finalItems.forEach((item:string) => {
            let prevIP = item.split('.')
            let finalIP = `${prevIP[0]}.${prevIP[1]}.${prevIP[2]}.${prevIP[3]}`
            haulTrucksList.value.push({
                title   : finalIP,
                value   : finalIP,
            })
        })
    })
    .catch(function (error) {
        showConsoleError(error);
    });

    finalListTruck.value = haulTrucksList.value;
}


const getHaulTruckPositions = async() => {
    clearLayerSource('LayerETH0','SourceETH0');
    clearLayerSource('LayerRPTPEER','SourceRPTPEER');
    clearLayerSource('LayerWLAN0','SourceWLAN0');
    clearLayerSource('LayerWLAN1','SourceWLAN1');
    clearLayerSource('LayerWLAN2','SourceWLAN2');
    clearLayerSource('LayerWLAN3','SourceWLAN3');
    clearLayerSource('LayerLATENCIA','SourceLATENCIA');

    selectedETH0.value      = true;
    selectedRPTPEER.value   = false;
    selectedWLAN0.value     = false;
    selectedWLAN1.value     = false;
    selectedWLAN2.value     = false;
    selectedWLAN3.value     = false;
    selectedLATENCIA.value  = false;
    showError.value         = false;
    showRangeSlider.value   = false;
    showCheckBoxes.value    = false;
    finalETH0Array.value    = [];
    finalRPTPEERArray.value = [];  
    finalWLAN0Array.value   = [];  
    finalWLAN1Array.value   = [];  
    finalWLAN2Array.value   = [];  
    finalWLAN3Array.value   = [];  
    finalLATENCIAArray.value = [];  
    let params = {
        ip : haulTruckSelected.value
    }
    
    let api = CONSTAPIS.APIS.value.drive.getInfoDriveRAJANTByIP;
    await axios.get(api,{params:params}).then(async(response)=>{
        /* eslint-disable */
        let finalItems:any = Papa.parse(response.data.body, {
            header: true, // Usa la primera fila como nombres de las claves en el JSON
            skipEmptyLines: true, // Ignorar líneas vacías
        });

        if(finalItems.data.length > 0) {
            let index = 0;

            finalItems.data.forEach((item:any) => {
                let prevETH0    = item.eth0 ? JSON.parse(item.eth0.replace(/'/g, '"')) : null;
                let prevRPTPEER = item.rptPeer ? JSON.parse(item.rptPeer.replace(/'/g, '"')) : null;
                let prevWLAN0   = item.wlan0 ? JSON.parse(item.wlan0.replace(/'/g, '"')) : null;
                let prevWLAN1   = item.wlan1 ? JSON.parse(item.wlan1.replace(/'/g, '"')) : null;
                let prevWLAN2   = item.wlan2 ? JSON.parse(item.wlan2.replace(/'/g, '"')) : null;
                let prevWLAN3   = item.wlan3 ? JSON.parse(item.wlan3.replace(/'/g, '"')) : null;
                index = index + 1;
                
                let lat   = 30.972732;
                let lng   = -110.34928166;

                if(item.latitud && item.latitud !== '')     { lat = parseFloat(item.latitud??'0'); }
                if(item.longitud && item.longitud !== '')   { lng = parseFloat(item.longitud??'0'); }
                if(item.gpsLat && item.gpsLat !== '')       { lat = parseFloat(item.gpsLat??'0'); }
                if(item.gpsLong && item.gpsLong !== '')     { lng = parseFloat(item.gpsLong??'0'); }

                let costeth0    = (prevETH0) ? prevETH0[2] : 0;
                let finalINFOeth0 = (prevETH0) ? `IP : ${prevETH0[0]} <br/> NOMBRE: ${prevETH0[1]} <br/> COSTO: ${prevETH0[2]} ` : '';

                let costrptpeer = (prevRPTPEER) ? prevRPTPEER[2] : 0;
                let finalINFOrptpeer = (prevRPTPEER) ? `IP : ${prevRPTPEER[0]} <br/> NOMBRE: ${prevRPTPEER[1]} <br/> COSTO: ${prevRPTPEER[2]} ` : '';

                let costwlan0   = (prevWLAN0) ? prevWLAN0[1] : 0;
                let finalINFOwlan0 = (prevWLAN0) ? `IP : ${prevWLAN0[0]} <br/> COSTO: ${prevWLAN0[1]} <br/> PEER RSSI: ${prevWLAN0[2]} <br/> SIGNAL: ${prevWLAN0[3]} <br/> NOISE: ${prevWLAN0[4]}` : '';
                
                let costwlan1   = (prevWLAN1) ? prevWLAN1[1] : 0;
                let finalINFOwlan1 = (prevWLAN1) ? `IP : ${prevWLAN1[0]} <br/> COSTO: ${prevWLAN1[1]} <br/> PEER RSSI: ${prevWLAN1[2]} <br/> SIGNAL: ${prevWLAN1[3]} <br/> NOISE: ${prevWLAN1[4]}` : '';

                let costwlan2   = (prevWLAN2) ? prevWLAN2[1] : 0;
                let finalINFOwlan2 = (prevWLAN2) ? `IP : ${prevWLAN2[0]} <br/> COSTO: ${prevWLAN2[1]} <br/> PEER RSSI: ${prevWLAN2[2]} <br/> SIGNAL: ${prevWLAN2[3]} <br/> NOISE: ${prevWLAN2[4]}` : '';

                let costwlan3   = (prevWLAN3) ? prevWLAN3[1] : 0;
                let finalINFOwlan3 = (prevWLAN3) ? `IP : ${prevWLAN3[0]} <br/> COSTO: ${prevWLAN3[1]} <br/> PEER RSSI: ${prevWLAN3[2]} <br/> SIGNAL: ${prevWLAN3[3]} <br/> NOISE: ${prevWLAN3[4]}` : '';
                
                let latencia    = item.latencia !== '' && item.latencia ? parseFloat(item.latencia) : 0; 
                let finalINFOLatencia = (latencia) ? `LATENCIA: ${latencia}ms ` : '';

                let coloreth0       = getColorByValue(costeth0, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorrptpeer    = getColorByValue(costrptpeer, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorwlan0      = getColorByValue(costwlan0, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorwlan1      = getColorByValue(costwlan1, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorwlan2      = getColorByValue(costwlan2, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorwlan3      = getColorByValue(costwlan3, [0,5000,10000],[COLORGREEN,COLORYELLOW, COLORRED]);
                let colorlatencia   = getColorByValue(latencia, [0,100,200],[COLORGREEN,COLORYELLOW, COLORRED]);

                prevETH0    ? postPORTItems('ETH0',lat, lng, coloreth0, costeth0 !== 0 ? finalINFOeth0: `Fuera de Linea`,index,item.fecha) : null;
                prevRPTPEER ? postPORTItems('RPTPEER',lat, lng, colorrptpeer, costrptpeer !== 0 ? finalINFOrptpeer: `Fuera de Linea`,index,item.fecha) : null;
                prevWLAN0   ? postPORTItems('WLAN0',lat, lng, colorwlan0, costwlan0 !== 0 ? finalINFOwlan0 : `Fuera de Linea`,index,item.fecha) : null;
                prevWLAN1   ? postPORTItems('WLAN1',lat, lng, colorwlan1, costwlan1 !== 0 ? finalINFOwlan1 : `Fuera de Linea`,index,item.fecha) : null;
                prevWLAN2   ? postPORTItems('WLAN2',lat, lng, colorwlan2, costwlan2 !== 0 ? finalINFOwlan2 : `Fuera de Linea`,index,item.fecha) : null;
                prevWLAN3   ? postPORTItems('WLAN3',lat, lng, colorwlan3, costwlan3 !== 0 ? finalINFOwlan3 : `Fuera de Linea`,index,item.fecha) : null;
                latencia   ? postPORTItems('LATENCIA',lat, lng, colorlatencia, finalINFOLatencia ,index,item.fecha) : null;
            })

            if(finalETH0Array.value.length > 0) {
                finalETH0Object.value = { "features" : finalETH0Array.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceETH0',finalETH0Object.value,'LayerETH0');
            }

            if(finalRPTPEERArray.value.length > 0) {
                finalRPTPEERObject.value = { "features" : finalRPTPEERArray.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceRPTPEER',finalRPTPEERObject.value,'LayerRPTPEER');
            }

            if(finalWLAN0Array.value.length > 0) {
                finalWLAN0Object.value = { "features" : finalWLAN0Array.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceWLAN0',finalWLAN0Object.value,'LayerWLAN0');
            }

            if(finalWLAN1Array.value.length > 0) {
                finalWLAN1Object.value = { "features" : finalWLAN1Array.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceWLAN1',finalWLAN1Object.value,'LayerWLAN1');
            }

            if(finalWLAN2Array.value.length > 0) {
                finalWLAN2Object.value = { "features" : finalWLAN2Array.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceWLAN2',finalWLAN2Object.value,'LayerWLAN2');
            }

            if(finalWLAN3Array.value.length > 0){
                finalWLAN3Object.value = { "features" : finalWLAN3Array.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceWLAN3',finalWLAN3Object.value,'LayerWLAN3');
            }

            if(finalLATENCIAArray.value.length > 0){
                finalLATENCIAObject.value = { "features" : finalLATENCIAArray.value, type : "FeatureCollection" };
                await setSourceAndLayer('SourceLATENCIA',finalLATENCIAObject.value,'LayerLATENCIA');
            }

            clearLayerSource('LayerRPTPEER','SourceRPTPEER');
            clearLayerSource('LayerWLAN0','SourceWLAN0');
            clearLayerSource('LayerWLAN1','SourceWLAN1');
            clearLayerSource('LayerWLAN2','SourceWLAN2');
            clearLayerSource('LayerWLAN3','SourceWLAN3');
            clearLayerSource('LayerLATENCIA','SourceLATENCIA');
            showCheckBoxes.value = true;
        }
        else {
            showError.value = true;
            showCheckBoxes.value = false;
        }
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const postPORTItems = (tipo:string, latitud:number, longitud:number, color:string, valor:string, index:number, fecha:string) => {
    let finalITEM = {                        
            "type": "Feature",
            "properties": {
                "level": 0,
                "name": `AllGraph${tipo}`,
                "height": 40,
                "base_height": 0,
                "color": color,
                "center": [longitud,latitud],
                "valor": valor,
                "fecha" : fecha
            },
            "geometry": {
                "coordinates": [ getFinalPositions(latitud,longitud) ],
                "type": "Polygon"
            },
            'id': index 
    }
    if(tipo.toUpperCase() === 'ETH0')       { finalETH0Array.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RPTPEER')    { finalRPTPEERArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'WLAN0')      { finalWLAN0Array.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'WLAN1')      { finalWLAN1Array.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'WLAN2')      { finalWLAN2Array.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'WLAN3')      { finalWLAN3Array.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'LATENCIA')   { finalLATENCIAArray.value.push(finalITEM) }
}

const setSourceAndLayer = async (source:string, alldata:DRIVE_OBS, layer:string) => {

    const mySource = map.getSource(source);
    if(mySource === undefined){ 

        map.addSource(source, { 'type': 'geojson', 'data': alldata});        
        map.addLayer({
            'id': layer,
            'type': 'fill-extrusion',
            'source': source,
            'paint': {
                'fill-extrusion-color': ['get', 'color'],
                'fill-extrusion-height': ['get', 'height'],
                'fill-extrusion-base': ['get', 'base_height'],
                'fill-extrusion-opacity':1,
            }
        });
        
        const popup = new maplibregl.Popup({
            closeButton: false,
            closeOnClick: false
        });

        /* eslint-disable */
        map.on('mouseenter', layer, (e:any) => {
            map.getCanvas().style.cursor = 'pointer';
            
            const coordinates:any = [e.lngLat.lng, e.lngLat.lat];
            const valor = e.features[0].properties.valor ;
            let mensaje = `${valor}` 

            while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
            }

            popup.setLngLat(coordinates).setHTML(mensaje).addTo(map);
        });

        map.on('mouseleave', layer, () => {
            map.getCanvas().style.cursor = '';
            popup.remove();
        });
    }
    else{
        mySource.setData(alldata)
    }
}


watch(getValueStoreDRIVE,(a:string, b:string)=>{
    a !== b ? resetData() : null;
    finalListTruck.value = haulTrucksList.value;
})

const clearLayerSource = (layer:string, source:string) => {
    if(map.getLayer(layer)) { map.removeLayer(layer); }
    if(map.getSource(source)) { map.removeSource(source); }
}

const watchLayer = (selectedVar: Ref<boolean>, source: string, finalObject: Ref<DRIVE_OBS>, layer: string) => {
    watch(selectedVar, (state) => {
        if (state) {
            setSourceAndLayer(source, finalObject.value, layer);
        } else {
            clearLayerSource(layer, source);
        }
    });
}; 

watchLayer(selectedETH0,  'SourceETH0', finalETH0Object, 'LayerETH0');
watchLayer(selectedRPTPEER,  'SourceRPTPEER', finalRPTPEERObject, 'LayerRPTPEER');
watchLayer(selectedWLAN0, 'SourceWLAN0', finalWLAN0Object, 'LayerWLAN0');
watchLayer(selectedWLAN1, 'SourceWLAN1', finalWLAN1Object, 'LayerWLAN1');
watchLayer(selectedWLAN2, 'SourceWLAN2', finalWLAN2Object, 'LayerWLAN2');
watchLayer(selectedWLAN3, 'SourceWLAN3', finalWLAN3Object, 'LayerWLAN3');
watchLayer(selectedLATENCIA, 'SourceLATENCIA', finalLATENCIAObject, 'LayerLATENCIA');

const resetData = () => {
    arrayDatesCircle.value = [];
    haulTruckSelected.value = '';
    infoWindowVisible.value = false;
}
const color:STRINGS = {
    '#E04637' : '#lineStyleRed',
    '#F9DC4B' : '#lineStyleYellow',
    '#427739' : '#lineStyleGreen',
    '#CAC8C8' : '#lineStyleGrey',
    '#00A5E8' : '#lineStyleBlue',
}

const getAllCoordinates = (finalObject: Ref<DRIVE_OBS>) => {
    let prevCoordinates = '';
    if(Object.keys(finalObject.value).length > 0){
        finalObject.value.features.forEach((element:DRIVE_PROPS) => {
            const finalCoord1 = (element.geometry.coordinates[0][0]);
            const finalCoord2 = (element.geometry.coordinates[0][1]);
            const finalCoord3 = (element.geometry.coordinates[0][2]);
            const finalCoord4 = (element.geometry.coordinates[0][3]);
            const finalCoord5 = (element.geometry.coordinates[0][4]);
            let someCoordinates = `
                <Placemark>
                    <name>${element.id}</name>
                    <description> 
                        <b>Detalles: </b> <br/>
                        Posicion: ${element.properties.center}<br/>
                        Valor:${element.properties.valor}.<br/>
                        Fecha:${element.properties.fecha}.
                    </description>
                    <styleUrl>${color[element.properties.color??'#CAC8C8']}</styleUrl>
                    <MultiGeometry>
                        <Polygon>
                            <extrude>1</extrude>
                            <altitudeMode>relativeToGround</altitudeMode>
                            <outerBoundaryIs>
                                <LinearRing>
                                <coordinates>
                                    ${finalCoord1[0]},${finalCoord1[1]},0
                                    ${finalCoord2[0]},${finalCoord2[1]},0
                                    ${finalCoord3[0]},${finalCoord3[1]},0
                                    ${finalCoord4[0]},${finalCoord4[1]},0
                                    ${finalCoord5[0]},${finalCoord5[1]},0
                                </coordinates>
                                </LinearRing>
                            </outerBoundaryIs>
                        </Polygon>
                        <Polygon>
                            <extrude>1</extrude>
                            <altitudeMode>relativeToGround</altitudeMode>
                            <outerBoundaryIs>
                                <LinearRing>
                                <coordinates>
                                    ${finalCoord1[0]},${finalCoord1[1]},20
                                    ${finalCoord2[0]},${finalCoord2[1]},20
                                    ${finalCoord3[0]},${finalCoord3[1]},20
                                    ${finalCoord4[0]},${finalCoord4[1]},20
                                    ${finalCoord5[0]},${finalCoord5[1]},20
                                </coordinates>
                                </LinearRing>
                            </outerBoundaryIs>
                        </Polygon>
                    </MultiGeometry>
                </Placemark>
            `
            prevCoordinates = prevCoordinates + someCoordinates
        })
    }

    return prevCoordinates
}

const getFolderItems = (title:string,finalCoords:string) => {
    let folder = finalCoords === '' ? '' :  `
        <Folder> 
            <name> ${title} </name>
            ${finalCoords}
        </Folder>
    `
    return folder
}

const getKMZ = async () => {

    
 // Contenido del archivo KML
 let kmlContent = `<?xml version="1.0" encoding="UTF-8"?>
  <kml xmlns="http://www.opengis.net/kml/2.2">
    <Document>
      <name>EQUIPO : ${haulTruckSelected.value}</name>

    <Style id="lineStyleRed">
      <LineStyle>
        <color>ff00ffff</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ffff</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleYellow">
      <LineStyle>
        <color>ff00ffff</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ffff</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGreen">
      <LineStyle>
        <color>ff00ff00</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ff00</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGrey">
      <LineStyle>
        <color>ff808080</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff808080</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleBlue">
      <LineStyle>
        <color>ffff0000</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ffff0000</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>
    `
    const finalETH0Coords       = getAllCoordinates(finalETH0Object)
    const finalRPTPEERCoords    = getAllCoordinates(finalRPTPEERObject)
    const finalWLAN0Coords      = getAllCoordinates(finalWLAN0Object)
    const finalWLAN1Coords      = getAllCoordinates(finalWLAN1Object)
    const finalWLAN2Coords      = getAllCoordinates(finalWLAN2Object)
    const finalWLAN3Coords      = getAllCoordinates(finalWLAN3Object)
    const finalLATENCIACoords   = getAllCoordinates(finalLATENCIAObject)

    let finalFolderETH0         = getFolderItems('ETH0',finalETH0Coords)
    let finalFolderRPTPEER      = getFolderItems('RPTPEER',finalRPTPEERCoords)
    let finalFolderWLAN0        = getFolderItems('WLAN0',finalWLAN0Coords)
    let finalFolderWLAN1        = getFolderItems('WLAN1',finalWLAN1Coords)
    let finalFolderWLAN2        = getFolderItems('WLAN2',finalWLAN2Coords)
    let finalFolderWLAN3        = getFolderItems('WLAN3',finalWLAN3Coords)
    let finalFolderLATENCIA     = getFolderItems('LATENCIA',finalLATENCIACoords)
      
  let finalKmlConent = `
   </Document>
  </kml>
  `

  kmlContent = kmlContent + finalFolderETH0 + finalFolderRPTPEER + finalFolderWLAN0 + finalFolderWLAN1 + finalFolderWLAN2 + finalFolderWLAN3 + finalFolderLATENCIA + finalKmlConent;

//   Crear un Blob con el contenido del KML
  const blob = new Blob([kmlContent], { type: "application/vnd.google-earth.kml+xml" });
  const url = URL.createObjectURL(blob);

  // Crear un enlace y simular el clic para descargar
  const link = document.createElement("a");
  link.href = url;
  link.download = `KML-EQUIPO: ${haulTruckSelected.value}.kml`;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}

