const getYAML = async() => {
    await axios.get(CONSTAPIS.APIS.value.yaml.getYAML).then((yaml)=> {
    let cambium     = yaml.data.body.CAMBIUM;
    let cisco       = yaml.data.body.CISCO;
    let mikrotik    = yaml.data.body.MIKROTIK;
    let rajant      = yaml.data.body.RAJANT;

    let itemsCAMBIUMPrev :FINALYAML[]   = cambium   ? Object.entries(cambium) : [];
    let itemsCISCOPrev   :FINALYAML[]   = cisco     ? Object.entries(cisco) : [];
    let itemsMIKROTIKPrev:FINALYAML[]   = mikrotik  ? Object.entries(mikrotik) : [];
    let itemsRAJANTPrev  :FINALYAML[]   = rajant    ? Object.entries(rajant) : [];
    
