    finalCISCO.value        = getArrayYAML(itemsCISCOPrev);
    finalMIKROTIK.value     = getArrayYAML(itemsMIKROTIKPrev);
    finalRAJANT.value       = getArrayYAML(itemsRAJANTPrev);
    })
}

const getArrayYAML = (item:FINALYAML[]) => { 
    let memoryYAML:YAMLITEMS[] = []
    item.forEach((item:FINALYAML) => {
        memoryYAML.push({
            value   : item[0],
            name    : item[1].codename,
            state   : item[1].state
        })
    })
    return memoryYAML
}

