    showEdit.value      = true;
    index.value         = i;
    typed.value         = type;
    nameChange.value    = name;
    status.value        = stat;
}

const closeEditDialog = () => {
    showEdit.value      = false;
    index.value         = 0;
    typed.value         = '';
    nameChange.value    = '';
    status.value        = 'false';
}

const changeInfo = () => {
    if(typed.value === 'cam'){
        finalCAMBIUM.value[index.value-1].name      = nameChange.value;
        finalCAMBIUM.value[index.value-1].state     = status.value;
    }
    if(typed.value === 'raj'){
        finalRAJANT.value[index.value-1].name       = nameChange.value;
        finalRAJANT.value[index.value-1].state      = status.value;
    }
    if(typed.value === 'mik'){
