import axios from 'axios'
import { ref } from 'vue'
import { APIS_DASHBOARD, APIS_TOPOLOGY } from '@/constants/apis'
import { showConsoleError } from '@/functions/errors'
import { TYPES, STATUS } from '@/constants/labels'
import { ALARM_TYPE, EQUIPOS, OBJ_ALARM_TYPE, OBJ_OPERABILITY, OBJ_TOPOLOGY_STATUS, OPERABILITY } from '@/interfaces/SmartLink/Equipos'
const TIMEOUT = 10000

import store from '@/store';

export async function getAlarmDashboard() {
  
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_DASHBOARD.value.alarmtodash,{ timeout : TIMEOUT })
    .then((response) => {
        const finalData:ALARM_TYPE[] = response.data;
        const prevObj = ref<OBJ_ALARM_TYPE>({
          distri: [], 
          acceso: [],
          client: [],
        })
        const showGraphs = ref(finalData.length > 0)

        const arrayDistri = ref<ALARM_TYPE[]>([])
        const arrayAcceso = ref<ALARM_TYPE[]>([])
        const arrayClient = ref<ALARM_TYPE[]>([])
        if(finalData.length > 0){
            finalData.forEach((item:ALARM_TYPE)=>{
                if(item.subtipo ===TYPES.distri) arrayDistri.value.push(item)
                if(item.subtipo ===TYPES.acceso) arrayAcceso.value.push(item)
                if(item.subtipo ===TYPES.client) arrayClient.value.push(item)
            })

            prevObj.value = {
                distri : arrayDistri.value,
                acceso : arrayAcceso.value,
                client : arrayClient.value,
            }
        }
        
      statusAxios.value = { status: 200, data : prevObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}

