    </Style>

    <Style id="lineStyleYellow">
      <LineStyle>
        <color>ff00ffff</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ffff</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGreen">
      <LineStyle>
        <color>ff00ff00</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff00ff00</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleGrey">
      <LineStyle>
        <color>ff808080</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ff808080</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>

    <Style id="lineStyleBlue">
      <LineStyle>
        <color>ffff0000</color>
        <width>1</width>
      </LineStyle>
      <PolyStyle>
        <color>ffff0000</color> 
        <fill>1</fill>
      </PolyStyle>
    </Style>
    `
    const finalETH0Coords       = getAllCoordinates(finalETH0Object)
    const finalRPTPEERCoords    = getAllCoordinates(finalRPTPEERObject)
    const finalWLAN0Coords      = getAllCoordinates(finalWLAN0Object)
    const finalWLAN1Coords      = getAllCoordinates(finalWLAN1Object)
    const finalWLAN2Coords      = getAllCoordinates(finalWLAN2Object)
    const finalWLAN3Coords      = getAllCoordinates(finalWLAN3Object)
    const finalLATENCIACoords   = getAllCoordinates(finalLATENCIAObject)

    let finalFolderETH0         = getFolderItems('ETH0',finalETH0Coords)
    let finalFolderRPTPEER      = getFolderItems('RPTPEER',finalRPTPEERCoords)
    let finalFolderWLAN0        = getFolderItems('WLAN0',finalWLAN0Coords)
    let finalFolderWLAN1        = getFolderItems('WLAN1',finalWLAN1Coords)
    let finalFolderWLAN2        = getFolderItems('WLAN2',finalWLAN2Coords)
    let finalFolderWLAN3        = getFolderItems('WLAN3',finalWLAN3Coords)
    let finalFolderLATENCIA     = getFolderItems('LATENCIA',finalLATENCIACoords)
      
  let finalKmlConent = `
   </Document>
  </kml>
  `

  kmlContent = kmlContent + finalFolderETH0 + finalFolderRPTPEER + finalFolderWLAN0 + finalFolderWLAN1 + finalFolderWLAN2 + finalFolderWLAN3 + finalFolderLATENCIA + finalKmlConent;

//   Crear un Blob con el contenido del KML
  const blob = new Blob([kmlContent], { type: "application/vnd.google-earth.kml+xml" });
  const url = URL.createObjectURL(blob);

  // Crear un enlace y simular el clic para descargar
  const link = document.createElement("a");
  link.href = url;
  link.download = `KML-EQUIPO: ${haulTruckSelected.value}.kml`;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}

