<script setup lang="ts">
import axios from 'axios';
import { Ref, computed,  onMounted, ref, watch, ComputedRef } from 'vue';
import { useDisplay }       from 'vuetify';
import { useI18n }          from 'vue-i18n';

import { useStore }   from 'vuex';
const store  = useStore();
// COMPONENTS
import DialogComponent      from '@/components/SmartLink/dialog/dialogComponent.vue';
import hexagonNanoComponent from '@/components/principalPage/hexagonNanoComponent.vue';
import MessageComponent from '@/components/SmartLink/dashboard/MessageComponent.vue';
import rangeComponent from '@/components/SmartLink/dashboard/rangeComponent.vue';


import { LMap, LTileLayer, LPolyline, LPopup, LCircle, LMarker, LIcon, LTooltip, LControlZoom, LControl /*LHeatLayer*/  } from "@vue-leaflet/vue-leaflet";
import "/public/leaflet/leaflet.css";
// CONSTANTS
import CONSTMAPS            from '@/constants/maps';
import CONSTAPIS            from '@/constants/apis';
import CONSTANTIMGS         from '@/constants/images';
import { STRING_GENERAL }         from '@/constants/labels';
import { COLOR }          from '@/constants/colors';

// INTERFACES
import { MARKERS, POSITION, CIRCLE, CLUSTERING } from '@/interfaces/SmartLink/Map';
import { STRINGS }          from '@/interfaces/SmartLink/General';
import { EQUIPAMENTS }      from '@/interfaces/SmartLink/Equips';
import { TOPO_ITEMS }       from '@/interfaces/SmartLink/Topology';

// FUNCTIONS
import { PointExpression } from 'leaflet';
import { showConsoleError } from '@/functions/errors';

const props = defineProps( {
    statusEquips: {required: true, type: Array<EQUIPAMENTS>}
})

const { t }     = useI18n()
const active = ref(false);

const onClickOutside = () => {
    active.value = false;
}

const activeClick = () =>{
    active.value = true;
}


const projectName = computed(() => store.state.projectName);

const isChile = ref(projectName.value === 'SmartLinkAntucoya' || projectName.value === 'SmartLinkZaldivar')
// const nameProject   = ref( process.env.NODE_ENV === "production" ? `${projectName.value}/${projectName.value}` : `${projectName.value}` )
// const nameProject   = ref( `${projectName.value}` )
const finalURL      = ref('')
const statusMarkers:ComputedRef<EQUIPAMENTS[]> = computed(() => props.statusEquips);
const { smAndDown }                         = useDisplay();
const width                                 = ref('350');
const widthToolTip                          = ref('100');
const markerspmpsm:Ref<any[]>           = ref([]);

const zoomMap = ref(CONSTMAPS.APIKEYGOOGLEMAPS.zoom);
const centerMap:Ref<PointExpression> = ref([CONSTMAPS.latitud.value,CONSTMAPS.longitud.value])

const markersPMPSMwRajant:Ref<any[]>           = ref([]);
const markersPMPSMwPMPAP:Ref<any[]>            = ref([]);
const markersPMPAPwRajant:Ref<any[]>           = ref([]);
const markersPMPAPwPMPSM:Ref<any[]>            = ref([]);
const markersRajantwPMPAP:Ref<any[]>           = ref([]);
const markersRajantwPMPSM:Ref<any[]>           = ref([]);

const markersPMPSMwoRajant:Ref<any[]>          = ref([]);
const markersPMPSMwoPMPAP:Ref<any[]>           = ref([]);
const markersRajantwoPMPSM:Ref<any[]>          = ref([]);
const markersRajantwoPMPAP:Ref<any[]>          = ref([]);
const markersPMPAPwoRajant:Ref<any[]>          = ref([]);
const markersPMPAPwoPMPSM:Ref<any[]>           = ref([]);

const markersRajantwoPMPSMwoPMPAP:Ref<any[]>   = ref([]);

const ipMatchPMPSMwPMPAP:Ref<string[]>         = ref([]);
const ipMatchPMPSMwRajant:Ref<string[]>        = ref([]);
const ipMatchRajantwPMPAP:Ref<string[]>        = ref([]);

const markersFINALpmpsm:Ref<MARKERS[]>      = ref([]);
const markerspmpap:Ref<any>           = ref([]);
const dialog                                = ref(false);
const selectedIp                            = ref('');
const selectedName                      = ref('');
const selectedType                          = ref('');
const elementPMPSM:Ref<EQUIPAMENTS[]>       = ref([]);        
const elementRAJANT:Ref<EQUIPAMENTS[]>      = ref([]);
const markersrajant:Ref<any[]>          = ref([]);
const toPMPAP: Ref<any>                = ref({})
const polilynes:Ref<any>                 = ref([]);
const finalPolilynes                        = ref(polilynes.value);
const polilynesANY:Ref<any>                 = ref([]);
const finalPolilynesANY                        = ref(polilynesANY.value);
const finalStatus = ref('Ok')

watch(smAndDown,(valor:boolean) => {
    valor ? width.value = '100%' :  width.value = '350';
})

const COLORSTATUS:STRINGS    = {
    OK      : 'green',
    ALARM   : 'yellow',
    ALERT   : 'red',
    DOWN    : '#353634'
}

//==================================================//
//===================== METHODS ====================//
//==================================================//

/**
 * Format Color in Polylines
 * @param color 
 */
const optionsRect = (status:string, position:POSITION[]) => {

    let color = COLORSTATUS[status];

    return {
        path            : position,
        colorLine       : color,
        geodesic        : true,
        strokeColor     : color,
        strokeOpacity   : 0.8,
        strokeWeight    : 3,
        fillColor       : color,
        fillOpacity     : 0.8
    }
}


const matchPMPSMWithAny = async() =>{
    let i = 0.00003;
    
    elementPMPSM.value.forEach((pmpsm:EQUIPAMENTS)=>{
        i = i + 0.00003;
        const ipRaj         = ref('');
        const typeRaj       = ref('');
        const finalPosition = ref([0,0]);
        const equipFindRAJ  = ref(false);
        const nameRaj   = ref('');
        let anotacion   = pmpsm.anotacion !== null ? JSON.parse(pmpsm.anotacion??'{}') : {};
        if(anotacion.gps){
            markersrajant.value.find(({ip,position}) => {
                if(anotacion.gps == ip) {
                    finalPosition.value = position
                }
            })
        }
        else{
            if(pmpsm.latitud && pmpsm.longitud){
                finalPosition.value = [pmpsm.latitud,pmpsm.longitud];               
            }

            elementRAJANT.value.find(({name,latitud,longitud,ip,tipo}:EQUIPAMENTS)=>{
                if(name && latitud && longitud){
                    let firstName   = '';
                    let secondName  = '';

                    if(projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO' || projectName.value === 'SmartLinkHCGROUP') {
                        firstName   = name.split('-')[0];
                        secondName  = pmpsm.name?.split('M')[1]??'';
                    }
                    if(projectName.value === 'SmartLinkCMDIC') {
                        firstName   = name.split('-')[2];
                        secondName  = pmpsm.name?.split('-')[3]??'';
                    }
                    if(firstName === secondName){
                        equipFindRAJ.value  = true;
                        let finalPositions  = [latitud, longitud];
                        finalPosition.value = finalPositions;
                        nameRaj.value       = name;
                        ipRaj.value         = ip??'';
                        typeRaj.value       = tipo??'';
                    }
                }
            })
        }
        

        markerspmpsm.value.push({
            position: finalPosition.value,
            name    : pmpsm.name,
            ip      : pmpsm.ip,
            tipo    : pmpsm.tipo,
            padre   : pmpsm.padre,
            status  : getStatusEquipment(pmpsm.ip??''),
        })

        if(equipFindRAJ.value) {
            markersPMPSMwRajant.value.push({
                position    : finalPosition.value,
                nameSM      : pmpsm.name,
                ipSM        : pmpsm.ip,
                tipoSM      : pmpsm.tipo,
                statusSM    : getStatusEquipment(pmpsm.ip??''),
                nameRAJ     : nameRaj.value,
                ipRAJ       : ipRaj.value,
                tipoRAJ     : typeRaj.value,
                statusRAJ   : getStatusEquipment(ipRaj.value??''),
            })
            ipMatchPMPSMwRajant.value.push(ipRaj.value)
        }
        else{
            markersPMPSMwoRajant.value.push({
                position: finalPosition.value,
                name    : pmpsm.name,
                ip      : pmpsm.ip,
                tipo    : pmpsm.tipo,
                padre   : pmpsm.padre,
                status  : getStatusEquipment(pmpsm.ip??'')
            })
        }
        let finalposition     = finalPosition.value;
        const equipFindPMPAP  = ref(false);

        markerspmpap.value.find(({position,name,ip,tipo,status}:any) =>{ 
            if(finalposition[0] - position[0] <= 0.0005 && finalposition[0] - position[0] >= -0.0005){
                equipFindPMPAP.value = true;
                markersPMPSMwPMPAP.value.push({
                    position    : position,
                    nameAP      : name,
                    ipAP        : ip,
                    tipoAP      : tipo,
                    statusAP    : status,
                    namePMPSM   : pmpsm.name,
                    ipPMPSM     : pmpsm.ip,
                    tipoPMPSM   : pmpsm.tipo,
                    statusPMPSM : getStatusEquipment(pmpsm.ip??''),
                })
                ipMatchPMPSMwPMPAP.value.push(pmpsm.ip??'')
            }
        })

