<template> 
    <v-sheet color="transparent" class="containerAreaSheet ma-4" >
        <EasyDataTable
        v-if="finalData.length > 0"
        table-class-name="customize-table"
        show-index
        :headers="finalHeader"
        :items="finalData"
        :rows-per-page="25"
        @click-row="showDialog"
        >
       <template
        v-for="n in maxCountHeader"
        :key="n"
        v-slot:[`item-radio${n}`]="item"
        >
            <span  v-if="item[`ip${n}`]">
                {{ item[`nombre${n}`] }} - {{ item[`ip${n}`] }}
            </span>
        </template> 
        <template
        v-for="n in maxCountHeader"
        :key="n"
        v-slot:[`item-estado${n}`]="item">
            <span v-if="item[`ip${n}`]">
                <v-icon :color="getColorStatus(item[`latencia${n}`])">mdi-circle</v-icon>
            </span>
        </template>    
        
        </EasyDataTable>

    </v-sheet>

    <v-dialog v-model="dialogValue" width="90%">
        <ResetDialog
        :finalIPS="finalIPS"
        />
    </v-dialog>
</template>


<script setup lang="ts">
import { LABELS } from '@/constants/labels';
import { getClientStatusData } from '@/services/Smartlink/Reset';
import { onMounted, ref } from 'vue';
import { useStore }         from 'vuex';
const store         = useStore();
import { useI18n }          from 'vue-i18n';
const { t }         = useI18n();
import { RESET_HEADER } from '@/constants/tableHeaders'
import ResetDialog from '@/components/SmartLink/dialog/ResetDialog.vue';
import { getColorStatus } from '@/functions/project'
import { HEADER } from '@/interfaces/SmartLink/Tables';
import { CLIENT_STATUS_TABLE } from '@/interfaces/SmartLink/Equipos';


const finalData = ref([])
const finalHeader = ref(RESET_HEADER)
const finalIPS  = ref<string[]>([])
const dialogValue = ref(false)
const maxCountHeader = ref(0)

async function getAllClientStatusData(){
    const allData = await getClientStatusData()
    
    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        finalData.value = allData.data.allArrays
        maxCountHeader.value = allData.data.maxCount
    }
}

function showDialog(item:CLIENT_STATUS_TABLE){
     finalIPS.value = Object.entries(item)
    .filter(([key]) => key.startsWith("ip"))
    .map(([, value]) => value)
    dialogValue.value = true;
}


async function getAllHeaderTable(){
    const head = ref<HEADER[]>([])
    for(let i = 1; i<= maxCountHeader.value; i++){
        head.value.push(
            { text: `Radio #${i}`,           value: `radio${i}`,         sortable: true},
            { text: `Estado #${i}`,          value: `estado${i}`,         sortable: true},
            { text: `Ultima Fecha #${i}`,    value: `fecha${i}`,         sortable: true},
        )
    }
    finalHeader.value = head.value
}


onMounted(async()=>{
   await getAllClientStatusData();
   await getAllHeaderTable()
})

</script>