import axios from 'axios'
import { ref } from 'vue'
import { showConsoleError } from '@/functions/errors'
import { APIS_TOPOLOGY, APIS_DASHBOARD } from '@/constants/apis'
import { TYPES, STATUS, STRING_GENERAL } from '@/constants/labels'
import { ALARM_TYPE, EQUIPOS, GLOBAL_STATUS, OPERABILITY } from '@/interfaces/SmartLink/Equipos'

const TIMEOUT = 10000



export async function getOperabilityLastDayTopology() {
  
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_TOPOLOGY.value.getAllOpLastDay,{ timeout : TIMEOUT })
    .then((response) => {
        const finalData:OPERABILITY[] = response.data;
        const finalObj = ref<OPERABILITY[]>([])
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalObj.value = finalData
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}


