<script setup lang="ts">
import { onMounted, ref, Ref } from 'vue'
import axios        from 'axios'

// COMPONENTS
import CONSTAPIS    from '@/constants/apis'

// INTERFACES
import { DBSTATUS } from '@/interfaces/SmartLink/Admin'
import { showConsoleError } from '@/functions/errors'

const databaseInfo:Ref<DBSTATUS> = ref({})
        
onMounted(async() => {
    /**
     * Captura los valores de la API: 'getDataBaseStatus'
     */
    await axios.get(CONSTAPIS.APIS.value.general.getDBStatus, { timeout: 10000 }).then((res)=>{
        databaseInfo.value = res.data[0];
    })
    .catch(function (error) {
        showConsoleError(error);
    });
})

</script>

<style scoped>
    .dbCard {
        border-radius: 20px;
    }
</style>