<template>
    <v-sheet :width="width"  class="card-login" color="bgCard">
        
        <v-row class="ma-0" style="align-items: center;" justify="center">

            <!-- SmartLink Logo -->
            <v-col cols="12" sm="5" md="5" lg="5" xl="5">
                <v-img :height="xs ? 70 : 100" style="flex:0 0 auto !important" class="pt-3 SMLogo"  src="@/assets/Images/logo.png"/>
            </v-col>
            <v-col cols="12" sm="2" md="2" lg="2" xl="2" class="pa-sm-0 mt-sm-0 mt-n5" style="text-align: center;">
                <span class="mx-6"> BY </span>
            </v-col>

            <!-- HCGROUP Logo -->
            <v-col cols="12" sm="5" md="5" lg="5" xl="5" style="text-align: center;">
                <img
                    height="80"
                    alt="Logo Smart Link"
                    :src="require('@/assets/logohcgif.gif')"
                />
            </v-col>

        </v-row>

        <v-divider class="my-5" :thickness="1"></v-divider>

        <!-- Form Login -->
        <v-form v-model="valid" @submit.prevent="submit">
            <v-container>
                <v-row>

                    <v-col cols="12">
                    <v-text-field
                        v-model="userName"
                        :rules="fieldUserRules"
                        :label="t('labels.user')"
                        prepend-icon="mdi-account-hard-hat"
                        variant="outlined"
                        required
                    ></v-text-field>
                    </v-col>

                    <v-col cols="12">
                    <v-text-field
                        v-model="password"
                        :rules="fieldPassRules"
                        :type="typePassword"
                        :append-inner-icon="iconAppend"
                        :label="t('labels.pass')"
                        prepend-icon="mdi-lock"
                        variant="outlined"
                        @click:append-inner="changeType()"
                        required
                    ></v-text-field>
                    </v-col>
                
                </v-row>
            </v-container>
            <v-divider class="my-5" :thickness="1"></v-divider>
            <v-btn type="submit" color="bgCircular" block class="btnLogin mt-2 mb-5 text-white" :disabled="!valid">{{t('buttons.login')}}</v-btn>
        </v-form>
    </v-sheet>
</template>

<script setup lang="ts">
import { Ref, computed, ref } from 'vue'
import router           from '@/router';
import axios            from 'axios';
import { useStore }     from 'vuex';
import { useDisplay }   from 'vuetify';
import { useI18n }      from 'vue-i18n';

// Component
import CONSTAPIS        from '@/constants/apis';
import { showConsoleError } from '@/functions/errors';
      
        //==================================================//
        //==================== VARIABLES ===================//
        //==================================================//

        /**
         * Vue / Vuetify
         */
        const { t }                     = useI18n();
        const store                     = useStore();
        const {xs, md, smAndDown}       = useDisplay();

        /**
         * Component
         */
        const valid:Ref<boolean>        =   ref(false);
        const userName:Ref<string>      =   ref('');
        const password:Ref<string>      =   ref('');
        const iconAppend                =   ref('mdi-eye');
        const typePassword              =   ref('password');
        const boolPassword              =   ref(true);
        const width                     =   computed(() => md.value?'50%':smAndDown.value?'70%':'600px');

        /**
         * Rules Fields
         */
        const fieldPassRules = ref (
        [
            (value:string) => {
            if (value) return true

            return t('labels.errors.passRequired')
            }
            
        ]);
        const fieldUserRules = computed ( () =>
            [
            (v:string) => !!v || t('labels.errors.userRequired'),
            (v:string) => v.length >= 4 || t('labels.errors.userError'),
        ],
       )

       //==================================================//
       //===================== METHODS ====================//
       //==================================================//

       /**
         * Show password
         */
        const changeType = () =>{
            boolPassword.value = !boolPassword.value
            if(boolPassword.value){
                iconAppend.value        = 'mdi-eye';
                typePassword.value      = 'password';
            }else{
                iconAppend.value        = 'mdi-eye-off';
                typePassword.value      = 'text';
            }
        }

         /**
         * Post Login Form to API
         */
        const submit = async () => {
            const params = {
                user : userName.value,
                pass : password.value,
            }
            await axios.get(CONSTAPIS.APIS.value.general.login, { params : params, timeout: 10000 }).then(
                (element) =>{
                    let sesion = element.data.body;
                    
                    if(sesion.status === 'User Found'){
                        localStorage.setItem('user', sesion.user);
                        store.commit('setUserSesion',sesion.user);
                        localStorage.setItem('rol', sesion.rol);
                        store.commit('setUserRol',sesion.rol);
                        router.push('/smartlink/dashboard')
                        store.commit('setAppBarPage','smartlinkPage');
                    }
                    else {
                        let msg = sesion.status === 'User Not Found' ? t('snackbar.login.userNotFound') : t('snackbar.login.passWrong') ; 
                        
                        userName.value = '';
                        password.value = '';
                        store.dispatch('showSnackBar', {type: 'setSnackError', msg: msg})
                    }
                }
            )
            .catch(function (error) {
                showConsoleError(error);
            });
        }

</script>

<style scoped>
.card-login{
    border: 2px solid rgba(0, 0, 0, 0.1);
    padding: 0 10px;
    margin: 120px auto 0px;
    border-radius: 20px;
}

.btnLogin{
    border-radius: 20px;
}
.SMLogo{
    flex: 0.5 0 auto !important;
}
</style>