    axios.get(CONSTAPIS.APIS.value.report.operability,{ params : params, timeout : 10000}).then(async(res)=>{
        let allData             = res.data;
        let finalArray:SNR[]    = [];
        await allData.forEach((elem:SNR)=>{
            let cantTotal   = (elem.ok??0) + (elem.alarm??0) + (elem.alert??0) + (elem.down??0)
            let finalItem   = {
                ip      : elem.ip,
                name    : elem.name,
                tipo    : elem.tipo,
                ok      : parseFloat(((elem.ok??0)/cantTotal*100).toFixed(2)),
                alert   : parseFloat(((elem.alert??0)/cantTotal*100).toFixed(2)),
                alarm   : parseFloat(((elem.alarm??0)/cantTotal*100).toFixed(2)),
                down    : parseFloat(((elem.down??0)/cantTotal*100).toFixed(2)),
            }
            finalArray.push(finalItem)   
                                
        })

        //ordena por la cantidad de alarmas y solo capturamos las 10 primeras
        finalArray.sort((a:SNR, b:SNR) => (b.alarm??0) - (a.alarm??0))
        let finalData = finalArray.slice(0,10);

        operability.value = finalData;
        operability.value.forEach((item:SNR)=>{
            cantOK.value.push((item.ok??0));
            cantALARM.value.push((item.alert??0));                
            cantALERT.value.push((item.alarm??0));                
            cantDOWN.value.push((item.down??0));
            arrayIPs.value.push((item.name??''));
        })
    })
    .catch(function (error) {
