     */
    axios.get(CONSTAPIS.APIS.value.report.tenlatency,{ params : params, timeout : 10000 }).then(async(res)=>{
        
        showTenLAT.value    = true;

        let allData         = res.data;
        if(allData.length > 0){
            showTenLAT.value    = true;
            let totalData = allData.sort((a:EQUIPAMENTS, b:EQUIPAMENTS) => (b.lat??0) - (a.lat??0)) //ordena por la latencia
            latencyTen.value        = totalData;
        }else{
            showTenLAT.value    = false;
        }
    })
    .catch(function (error) {
        showTenLAT.value    = false;
        showConsoleError(error);
    });
  /* eslint-disable */

    /**
     * Obtiene valores de qualitylap de la API: "getWorstQualityLAP"
     * Enviando parámetros como fecha inicial, fecha final, tipo y el tipo de Familia
     */
    axios.get(CONSTAPIS.APIS.value.report.qualitylap,{ params : params, timeout : 10000 }).then(async(res)=>{
        
        showQualityLap.value    = true;

        let finalArray:SNR[]  = [];
        let allData             = res.data;
        let allRADIO:any        = {}
        let allAVG:any          = {}
        let RADIOrx             = 0;
        let AVGtx               = 0;
        if(allData.length > 0){
            allData.forEach((elem:any) => {

                if(projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkHCGROUP'){
                    // Convierte a JSON la variable "snr"
                    // Si no trae valores, convierte el valor por defecto: "{"V": 0, "H":0, "PER": 0}"
                    // Se aumentó PER : 0, por MIMOSA en Cananea
                    allRADIO = JSON.parse(elem.snr??`{"V": 0, "H": 0, "PER": 0}`);

                    // Como trae valores simples o en un array, primero se valida
                    // Si existe y si es un objeto (un array), se captura el primer valor del array,
                    // Caso contrario se coloca el valor único que tiene
                    RADIOrx = (typeof allRADIO['H']).toUpperCase() == 'OBJECT' && allRADIO['H'] !== null  ? allRADIO['H'][1] : allRADIO['H'];

                    AVGtx   = allRADIO['PER'];
                }
                else{
                    // Convierte a JSON la variable "link_radio"
                    // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx":0}"
                    allRADIO    = JSON.parse(elem.link_radio??`{"tx": 0, "rx":0}`);

                    // Como trae valores simples o en un array, primero se valida
                    // Si existe y si es un objeto (un array), se captura el primer valor del array,
                    // Caso contrario se coloca el valor único que tiene
