        let SNRV                = 0;
        let SNRH                = 0;
        let AVGtx               = 0;

        allData.forEach((elem:ALIGN)=>{

            // Convierte a JSON la variable "snr"
            // Si no trae valores, convierte el valor por defecto: "{"V": 0, "H":0, "PER": 0}"
            // Se aumentó PER : 0, por MIMOSA en Cananea
            allSNR      = JSON.parse(elem.snr??`{"V": 0, "H": 0, "PER": 0}`);

            // Si es Cananea, el valor está en el index 3 del array, si es otro proyecto, será el primer valor
            let indexNumber = (projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO') ? 3 : 0;

            // Como trae valores simples o en un array, primero se valida
            // Si existe y si es un objeto (un array), se captura el primer valor del array,
            // Caso contrario se coloca el valor único que tiene
            SNRV        = (typeof allSNR['V']).toUpperCase() == 'OBJECT' && allSNR['V'] !== null ? allSNR['V'][indexNumber] : allSNR['V'];
            SNRH        = (typeof allSNR['H']).toUpperCase() == 'OBJECT' && allSNR['H'] !== null ? allSNR['H'][indexNumber] : allSNR['H'];

            // Convierte a JSON la variable "avgpower"
            // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx": 0}"
            allAVG      = JSON.parse(elem.avgpower??`{"tx": 0, "rx":0}`);

            // Como trae valores simples o en un array, primero se valida
            // Si es Cananea, Captura el valor de PER del SNR, si es otro proyecto, valida
            // si existe y si es un objeto (un array), se captura el primer valor del array,
            // Caso contrario se coloca el valor único que tiene
            AVGtx       = (projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO') ? allSNR['PER'] : (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null ? allAVG['tx'][0] : allAVG['tx'];

            let finalItemSNRH:SNR = {
                fecha       : elem.fecha??'',
                name        : elem.name??'',
                ip          : elem.ip??'',
                snr_h       : SNRH??0,
                tipo        : elem.tipo??'',
                avgpower    : AVGtx??0
            }

            let finalItemSNRV:SNR = {
                fecha       : elem.fecha??'',
                name        : elem.name??'',
                ip          : elem.ip??'',
                snr_v       : SNRV??0,
                tipo        : elem.tipo??'',
