        SNR_HORIZ.value.length > 0 ? showQualitySNRH.value = true : showQualitySNRH.value = false;
    })
    .catch(function (error) {
        showQualitySNRV.value = false;
        showQualitySNRH.value = false;
        showConsoleError(error);
    });
