/**
 * Mostrar el Dialogo
 */
const dialog        = ref(false);

/**
 * IP del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedIp    = ref('');

/**
 * Nombre del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedName  = ref('');

/**
 * Tipo del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedType  = ref('');

/**
 * Captura (ip, tipo, nombre) y lo almacena en las variables correspondientes
 * para luego abrir el dialog con la información gráfica y estadística
 * del equipo en cuestión
 * @param ip IP del equipo Seleccionado
 * @param name Nombre del equipo Seleccionado
 * @param type Tipo del equipo Seleccionado
 */
const showDialog = (ip:string, name:string, type:string) => {
    selectedIp.value    = ip;
    selectedName.value  = name;
    selectedType.value  = type;
    dialog.value        = true;
}

/**
 * Cierra el Diálogo mediante un EMIT del componente de Dialogo
 * @param value valor FALSO
 */
const closeDialog = (value:boolean) => {
    dialog.value = value;
}


/**
 * Si hay un cambio de fecha inicial o final, se ocultará toda la info
 */
watch(dateini,()=>{ showInfo.value = false; })
watch(datefin,()=>{ showInfo.value = false; });



const reportPDF:any                     = ref();
const exportToPdf = async () => {
    window.print();
}


