<template>        
    <v-row class="ma-0">
        <v-col class="pa-0" cols="12" sm="12" md="12" lg="12" xl="12" xxl="12">
            <v-card class="ma-4 pa-4 mb-0 mt-0 py-0" style="border-radius: 10px;" elevation="0" color="bgtopology">
                <v-card-title>
                    <v-row justify="space-between" class="ma-0" style="align-items: center;">
                        <span class="titleDashboard"> {{ t('labels.linkMap') }} </span>                           
                        <v-progress-circular
                        class="circular_btn"
                        :rotate="360"
                        :size="25"
                        :width="5"
                        :model-value="valorCount"
                        color="bgCircular"
                        @click="restarInfo()"
                        />
                    </v-row>                             
                </v-card-title>
                <v-card-text class="pr-0" :class="statusEquips.length > 0 ? '' : 'text-center'">                        
                    <NetworkChart
                    v-if="showNetWork"
                    :statusEquips="statusEquips"
                    />
                    <Prog_circular v-else/>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>

    <v-row class="ma-0">
        <v-col cols="12" class="pa-0">
            <KPICOMPONENT v-if='showKPI' :recharge="changeKPIData" :countEquipStatus='countEquipStatus'/>
        </v-col>
    </v-row>
    
    <v-row class="ma-0">
        
        <v-col cols="12" class="pa-0 ">
            <v-sheet v-if="showVirMac" class="defaultCard" color="bgtopology" >
                <span class="ml-4 titleDashboard">{{ t('sidebar.servers') }}</span>
                <VirtualMachines 
                v-if="ipVIRMAC.length > 0"               
                :ip="ipVIRMAC"
                :recharge="changeMVSData"
                :emited="false"
                :rows=7
                />
            </v-sheet>
        </v-col>
    </v-row>
</template>

<script setup lang="ts">
import { Ref,  onBeforeUnmount, onMounted, ref } from 'vue';
import { useStore }         from 'vuex';
import { useI18n }          from 'vue-i18n';

// COMPONENTS
import NetworkChart         from '@/components/SmartLink/maps/NetworkChart.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import KPICOMPONENT         from '@/components/SmartLink/dashboard/KPI-Component.vue';
import VirtualMachines      from '@/components/SmartLink/server - lte/VirMacDialog.vue';

// INTERFACES
import { SystemStatus} from '@/interfaces/SmartLink/Equips';
import { TOPO_GENERAL,}             from '@/interfaces/SmartLink/Topology';
import { getAllStatusData } from '@/services/Smartlink/Principal';
import { getIPServerData } from '@/services/Smartlink/Server';
import { LABELS } from '@/constants/labels';


const store         = useStore(); 
const { t }         = useI18n();

/**
 * Valores del mapa Traídos de la API
 */
const statusEquips:Ref<TOPO_GENERAL[]>      = ref([]);  

/**
 * Objeto con la cantidad de estados según el subtipo
 */
const countEquipStatus:Ref<SystemStatus>    = ref({
    distri: { ok: 0, alarm: 0, alert: 0, down: 0},
    acceso: { ok: 0, alarm: 0, alert: 0, down: 0},
    client: { ok: 0, alarm: 0, alert: 0, down: 0},
});

/**
 * Muestra El componente de Network (Mapa) si desde la API
 * traen información
 */
const showNetWork   = ref(true);

/**
 * Muestra El componente de KPI si desde la API
 * traen información
 */
const showKPI       = ref(false);  


/**
 * Captura toda la info de la API: "getAllTopologyData"
 * lo almacena en la variable "statusEquips".
 * Calcula la cantidad de equipos en según que estados,
 * filtrando por su familia, lo almacena en "countEquipStatus"
 */
const getStatusData = async () => {

    const allData = await getAllStatusData()
    
    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        countEquipStatus.value = allData.data.countEquipData
        statusEquips.value = allData.data.statusEquipData
    }
    showKPI.value = allData.showGraphs
    // showNetWork.value = allData.showGraphs
}

/**
 * Para recargar el Componente KPI
 */
const changeKPIData     = ref(true);

/**
 * Para recargar el Componente Virtual Machine
 */
const changeMVSData     = ref(true);

/**
 * Valor numérico del ciclo 
 */
const valorCount             = ref(0);

/**
 * Valor final del INVERVALOR para el reset de la data
 */
const interval      = ref();

/**
 * Se activa cada 15 minutos o cuando le das click al círculo en la parte superior derecha del mapa.
 * Recarga la infomación de los componentes hijos. (KPI y Virtual Machine)
 */
const restarInfo = async() => {
    
    changeKPIData.value = false;
    changeMVSData.value = false;
    showKPI.value = false;
    showNetWork.value = false;
    valorCount.value    = 0;

    statusEquips.value  = [];
        
    getStatusData();
    getIpVirMac();

    // Hay un delay de 100ms para la carga de data
    setTimeout(()=> {
        changeKPIData.value = true;
        changeMVSData.value = true;
        showKPI.value = true;
        showNetWork.value = true;

    },100)
}


/**
 * Muestra El componente de Virtual Machine si desde la API
 * traen información
 */
const showVirMac    = ref(true);

/**
 * Array de IP de los servidores
 */
const ipVIRMAC:Ref<string[]> =    ref([])

/**
 * Consulta la API: "getIPServer"
 * Captura las IPs de los equipos de TIPO: SERVIDOR/SERVER
 * lo almacena en el ARRAY: "ipVIRMAC"
 */
const getIpVirMac = async() => { 

    const allData = await getIPServerData()

    showVirMac.value = allData.showGraphs
    
    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        ipVIRMAC.value = allData.data
    }
}

/**
 * Cuando se desmonta el componente (cuando le das a otra vista)
 * se reinicia el intervalo
 */
onBeforeUnmount(() => {
    clearInterval(interval.value)
}),

onMounted( async() => {
    
    getStatusData();   
    getIpVirMac();       

    /**
     * Crea un intervalo que dura 15 minutos
     * Cuando llega a 15 minutos, llama a la función "restarInfo()"
     */
    interval.value = setInterval(async()=>{
        if(valorCount.value > 100 ){ 
            await restarInfo(); 
        }
        valorCount.value += 10 / 6;
    },1000)
})

</script>

