import axios from 'axios'
import { ref, computed } from 'vue'
import { APIS_KPI } from '@/constants/apis'
import { showConsoleError } from '@/functions/errors'
import { TYPES } from '@/constants/labels'
import { VISIBLE_PAGES } from '@/constants/labels'

const TIMEOUT = 10000
import store from '@/store';
import { ALL_LATENCY, EQUIPOS, OPERABILITY, PARAMETROS } from '@/interfaces/SmartLink/Equipos'
const projectName = computed(() => store.state.projectName);




export async function getAllKPICostTableData(params:PARAMETROS) {
  
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()

  const finalAPI = params.kpi === VISIBLE_PAGES.wiredpeers ? APIS_KPI.value.getcostwired : APIS_KPI.value.getcostwireless
  
  await axios
    .get(finalAPI,{ timeout : TIMEOUT })
    .then((response) => {
        const finalData:EQUIPOS[] = response.data;
        const finalObj = ref<EQUIPOS[]>([])
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalData.forEach((item:EQUIPOS) => {
                const instamesh = JSON.parse(item.wireless??'');
                const finalItems = {
                    fecha      : item.fecha,
                    ip         : item.ip,
                    name       : item.name,
                    costo      : instamesh.cost ? instamesh.cost : 0
                }
                finalObj.value.push(finalItems);
            })
            showGraphs.value = finalObj.value.length > 0;
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}


export async function getAllKPITableData(params:PARAMETROS) {
  
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()

  
  await axios
    .get( APIS_KPI.value.getAllDataKPI,{ params: params, timeout : TIMEOUT })
    .then((response) => {
        const finalData = response.data;
        const finalObj = ref<EQUIPOS[]>([])
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){

          if(params.kpi === VISIBLE_PAGES.operability){
            finalData.forEach((item:OPERABILITY) => {
              const cantTotal   = (item.ok??0) + ( item.alarm??0) + (item.alert??0) + (item.down??0)
              const finalItem   = {
                  ip      : item.ip,
                  name    : item.name,
                  tipo    : item.tipo,
                  ok      : parseFloat(((item.ok??0)/cantTotal*100).toFixed(2)),
                  alert   : parseFloat(((item.alert??0)/cantTotal*100).toFixed(2)),
                  alarm   : parseFloat(((item.alarm??0)/cantTotal*100).toFixed(2)),
                  down    : parseFloat(((item.down??0)/cantTotal*100).toFixed(2)),
              }
              finalObj.value.push(finalItem)
            })
          }

          if(params.kpi === VISIBLE_PAGES.quality_snr){
            finalData.forEach((item:EQUIPOS)=>{

              // Convierte a JSON la variable "snr"
              // Si no trae valores, convierte el valor por defecto: "{"V": 0, "H":0, "PER": 0}"
              // Se aumentó PER : 0, por MIMOSA en Cananea
              const allSNR      = JSON.parse(item.snr??`{"V": 0, "H": 0, "PER": 0}`);

              // Si es Cananea, el valor está en el index 3 del array, si es otro proyecto, será el primer valor
              const indexNumber = (projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO') ? 3 : 0;

              // Como trae valores simples o en un array, primero se valida
              // Si existe y si es un objeto (un array), se captura el primer valor del array,
              // Caso contrario se coloca el valor único que tiene
              const SNRV        = (typeof allSNR['V']).toUpperCase() == 'OBJECT' && allSNR['V'] !== null ? allSNR['V'][indexNumber] : allSNR['V'];
              const SNRH        = (typeof allSNR['H']).toUpperCase() == 'OBJECT' && allSNR['H'] !== null ? allSNR['H'][indexNumber] : allSNR['H'];

              // Convierte a JSON la variable "avgpower"
              // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx": 0}"
              const allAVG      = JSON.parse(item.avgpower??`{"tx": 0, "rx":0}`);

              // Como trae valores simples o en un array, primero se valida
              // Si es Cananea, Captura el valor de PER del SNR, si es otro proyecto, valida
              // si existe y si es un objeto (un array), se captura el primer valor del array,
              // Caso contrario se coloca el valor único que tiene
              const AVGtx       = (projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO') ? allSNR['PER'] : (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null ? allAVG['tx'][0] : allAVG['tx'];

              const finalItem = {
                  fecha       : item.fecha??'',
                  name        : item.name??'',
                  ip          : item.ip??'',
                  snr_h       : SNRH??0,
                  snr_v       : SNRV??0,
                  tipo        : item.tipo??'',
                  avgpower    : AVGtx??0
              }
              finalObj.value.push(finalItem)                                            
            })
          }

          if(params.kpi === VISIBLE_PAGES.quality_lap){
            finalData.forEach((item:EQUIPOS) => {

              // let allSNR  = {"V": [], "H": [], "PER": 0, tx: [], rx: []};
              // let allAVG  = {"V": [], "H": [], "PER": 0, tx: [], rx: []};
              /* eslint-disable */
              let allSNR:any = {};
              let allAVG:any = {};
              let RADIOrx = 0;
              let AVGtx   = 0;

              if(projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO'){

                  // Convierte a JSON la variable "snr"
                  // Si no trae valores, convierte el valor por defecto: "{"V": 0, "H":0, "PER": 0}"
                  // Se aumentó PER : 0, por MIMOSA en Cananea
                  allSNR = JSON.parse(item.snr??`{"V": 0, "H": 0, "PER": 0}`);

                  // Como trae valores simples o en un array, primero se valida
                  // Si existe y si es un objeto (un array), se captura el primer valor del array,
                  // Caso contrario se coloca el valor único que tiene
                  RADIOrx = (typeof allSNR['H']).toUpperCase() == 'OBJECT' && allSNR['H'] !== null  ? allSNR['H'][1] : allSNR['H'];

                  AVGtx   = allSNR['PER'];
              }
              else{
                  // Convierte a JSON la variable "snr"
                  // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx":0}"
                  allSNR    =   JSON.parse(item.link_radio??`{"tx": 0, "rx":0}`);

                  // Como trae valores simples o en un array, primero se valida
                  // Si existe y si es un objeto (un array), se captura el primer valor del array,
                  // Caso contrario se coloca el valor único que tiene
                  RADIOrx       = (typeof allSNR['rx']).toUpperCase() == 'OBJECT' && allSNR['rx'] !== null  ? allSNR['rx'][0] : allSNR['rx'];

                  // Convierte a JSON la variable "avgpower"
                  // Si no trae valores, convierte el valor por defecto: "{"tx": 0, "rx":0}"
                  allAVG      = JSON.parse(item.avgpower??`{"tx": 0, "rx":0}`);

                  // Como trae valores simples o en un array, primero se valida
                  // Si existe y si es un objeto (un array), se captura el primer valor del array,
                  // Caso contrario se coloca el valor único que tiene
                  AVGtx       = (typeof allAVG['tx']).toUpperCase() == 'OBJECT' && allAVG['tx'] !== null  ? allAVG['tx'][0] : allAVG['tx'];
              }
              
              const finalItem = {
                  fecha           : item.fecha??'',
                  name            : item.name??'',
                  ip              : item.ip??'',
                  avgpower        : AVGtx.toString()??'',
                  link_radio_rx   : RADIOrx??0,
                  tipo            : item.tipo??'',
              }
              finalObj.value.push(finalItem) 
            })
          }

          if(params.kpi === VISIBLE_PAGES.temp_inst){
            finalData.forEach((item:EQUIPOS)=>{
              // Convierte a JSON la variable "valores"
              // Si no trae valores, convierte el valor por defecto: ""
              // Extrae el valor: ".temperature@", que siempre es un array
              const temp = JSON.parse(item.valores??'');
              const finalItem = {
                  fecha      : item.fecha,
                  ip         : item.ip,
                  name       : item.name,
                  temperatura: `${(temp.temperature[0]/100).toFixed(2)} °C`
              }
              finalObj.value.push(finalItem)                                            
            })
          }

          if(params.kpi === VISIBLE_PAGES.latency){
            finalObj.value = finalData
          }


          showGraphs.value = finalObj.value.length > 0
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}


export async function getAllLatencyData() {
  
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_KPI.value.getAllLatency,{ timeout : TIMEOUT })
    .then((response) => {
        const finalData:EQUIPOS[] = response.data;
        const finalObj = ref<ALL_LATENCY>({
          distri : [],
          acceso : [],
          client : [],
          downs  : [],
          all    : []
        })
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalData.forEach((item:EQUIPOS) => {
                if(item.latencia === -1){
                    finalObj.value.downs.push(item)
                    finalObj.value.all.push(item) 
                }
                else{
                  item.subtipo === TYPES.distri   ? finalObj.value.distri.push(item) : null;
                  item.subtipo === TYPES.acceso   ? finalObj.value.acceso.push(item) : null;
                  item.subtipo === TYPES.client   ? finalObj.value.client.push(item) : null;
                  finalObj.value.all.push(item)
                }
            })
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}