<template>
    <v-sheet color="bgtopology"  class="ma-4 defaultCard">
        <v-row class="ma-0" v-if="items.length > 0">
            <v-col class="pa-0 mb-4" cols="12" align-self="center" >                
                <span  class="titlePages">{{ t('labels.statistics') }}</span>
            </v-col>
            <v-col cols="8">
                <EasyDataTable                
                show-index
                table-class-name="customize-table"
                :headers="headers"
                :items="items"
                :rows-per-page="10"
                :hide-footer="false"
                @click-row="showDataInfo"
                >         
                </EasyDataTable>
            </v-col>
            <v-col cols="4">
                <v-row  v-if="sendData.length > 0">
                    <v-col v-for="(item, i) in sendData" :key="i" cols="6" class="pa-0">
                        <v-sheet color="transparent" class="cardStatisticsLTE py-3">
                            <v-row class="ma-0" justify='space-around'> 
                                <span>{{item.ifDescr}}</span> 
                                <v-icon icon="mdi-chart-bell-curve-cumulative" size="small" @click="showDialog(item.ifDescr)"/>
                            </v-row>
                            <table>
                                <tr>
                                    <td>MTU (Bytes)</td>
                                    <td class="pl-4">{{item.ifMtu}}</td>
                                </tr>
                                <tr>
                                    <td>Speed (Mbps)</td>
                                    <td class="pl-4">{{parseInt(item.ifSpeed)/1000000}}</td>
                                </tr>
                                <tr>
                                    <td>RX (MBytes)</td>
                                    <td class="pl-4">{{item.RXBytes}}</td>
                                </tr>
                                <tr>
                                    <td>TX (MBytes)</td>
                                    <td class="pl-4">{{item.TXBytes}}</td>
                                </tr>
                            </table>
                        </v-sheet>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>        
        <v-row v-else justify="center" class="ma-4">
            <Prog_circular/>
        </v-row>
    </v-sheet>
    <v-dialog v-model="dialog" width="85%" >

        <v-card class="card_dialog" color="background"> 
        <v-row class="ma-0" justify="space-between" style="align-items: self-end !important;" >
            <v-col cols="10">
                <span :class="xs ? 'textDashboard' : 'subtitleDashboard'"> {{ selectedIP }} - {{ selectedNAME }}</span>
            </v-col>
            <v-col cols="1">
                <v-btn icon="mdi-close" variant="plain" @click="closeDialog"></v-btn>
            </v-col>
        </v-row>
        <v-divider class="mt-7"></v-divider>
        <v-sheet color="transparent">
            <StatisticsComponent
            :rxbytes="finalRXBYTES"
            :txbytes="finalTXBYTES"
            />
        </v-sheet>  
    </v-card>
        
    </v-dialog>
</template>

<script setup lang="ts">

import { ref, Ref, onMounted }  from 'vue';
import { useStore }             from 'vuex';
import { HEADER, ALLHEADER }    from '@/interfaces/SmartLink/Tables';
import StatisticsComponent      from '@/components/GestorHCGLink/dialogs/statisticsComponent.vue';
import { useDisplay }           from 'vuetify';
import { useI18n }          from 'vue-i18n';
import Prog_circular                            from '@/components/PrincipalComponents/Prog_circular.vue';

//Interfaces
import { ALLDATAINFO, ENLACES, LASTENLACEINFO, TABLEINFO } from '@/interfaces/GestorHCGLINK/Equips';

import { LABELS } from '@/constants/labels';
import { getItemsTablesGestor, getLast30DaysLTEGestor, getLastDatesLTEGestor, getMaxDatesLTEGestor } from '@/services/Gestor/Gestor';


const { xs }            = useDisplay();
const { t }             = useI18n();
const store                     = useStore();
const items: Ref<TABLEINFO[]>                     = ref([]);
const headers:Ref<HEADER[]>     = ref([]);
const HEADERS__PRIMARY_TABLE:ALLHEADER = {
    header : [
        { text: 'IP Address',   value: "ip",            sortable: true },
        { text: 'Nombre',       value: "name",          sortable: true },
        { text: 'Tipo',         value: "tipo",          sortable: true },
        { text: 'Subitpo',      value: "subtipo",       sortable: true },
        { text: 'Mark',         value: "marca",         sortable: true },
    ]
};
headers.value = HEADERS__PRIMARY_TABLE['header'];

const sendData:Ref<ENLACES[]> = ref([])



const finalInfoData:Ref<LASTENLACEINFO> = ref({})
const finalInfoData30Days:Ref<ALLDATAINFO> = ref({});
const selectedIP        = ref('');
const selectedNAME      = ref('');
const arrayDates:Ref<string[]>   = ref([]);
// const showDataTable     = ref(false);

const getMaxDatesLTE = async() => {

    const allData = await getMaxDatesLTEGestor()

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        arrayDates.value = allData.data
    }
}



const getLastDatesLTE = async() => {
    let params = {
        dates  : arrayDates.value
    }    
    sendData.value = [];
    

    const allData = await getLastDatesLTEGestor(params)

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        finalInfoData.value = allData.data
    }
}

const getAllDaysLTE = async() => {


    const allData = await getLast30DaysLTEGestor()

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        finalInfoData30Days.value = allData.data
    }
}

const showDataInfo = (item:TABLEINFO) => {
    sendData.value = [];
    sendData.value = finalInfoData.value[item.ip] ? finalInfoData.value[item.ip] : [] ;
    selectedIP.value = item.ip;
}

const finalRXBYTES:Ref<number[][]> = ref([]);
const finalTXBYTES:Ref<number[][]> = ref([]);
const dialog       = ref(false);

const showDialog = (name:string) => {
    let selectedInfo = finalInfoData30Days.value[selectedIP.value][name]
    selectedNAME.value = name
    finalRXBYTES.value = selectedInfo.rxbytes;
    finalTXBYTES.value = selectedInfo.txbytes;
    dialog.value = true;
}

const closeDialog = () => {
    dialog.value = false;
}

const getItems = async() => {

    const allData = await getItemsTablesGestor()

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        items.value = allData.data
    }
}

onMounted(async () => {
    getItems() 
    await getAllDaysLTE();
    await getMaxDatesLTE();
    await getLastDatesLTE();
    // setTimeout(()=>{
    // }, 1000)
})

</script>

<style scoped>
.cardStatisticsLTE{
    border-top: 2px solid rgb(var(--v-theme-colorText)) !important;
}
</style>