import axios from 'axios'
import { createStore } from 'vuex'
import CONSTAPIS from '@/constants/apis'

export default createStore({
  state: {
    sesion:{
      user    : '',//
      rol     : '',//
    },
    snackbar:{
      type    : '',
      msg     : '',
      valor   : false
    },
    valueKPI            : '',//
    arrayListItems      : [],//
    
    //Variable de tipo TOPO_GENERAL
    arrayItemsByStatus  : [],

    arrayTotalKPI       : [],//
    overlayLoading      : false,//
    snackbarAdd         : false,//
    snackbarEdit        : false,//
    snackbarDelete      : false,//
    snackbarError       : false,//
    snackbarmsg         : '',//
    drawer              : true,//
    showVirMac          : true,//
    ieoDistri           : '',//
    ieoAcceso           : '',//
    ieoClient           : '',//
    appBarPage          : 'loginPage',//
    projectName         : `${process.env.VUE_APP_NAME_PROJECT}`,
    projectLAT          : parseFloat(process.env.VUE_APP_MAP_LAT??''),
    projectLNG          : parseFloat(process.env.VUE_APP_MAP_LNG??''),
    projectZOOM         : parseFloat(process.env.VUE_APP_MAP_ZOOM??''),
  },    
  mutations: {
    // desde login, AppBar
    setAppBarPage (state, page){
      state.appBarPage = page
    },
    setProjectName (state, project){
      state.projectName = project
    },
    setProjectLAT (state, latitud){
      state.projectLAT = latitud
    },
    setProjectLNG (state, longitud){
      state.projectLNG = longitud
    },
    setProjectZOOM (state, zoom){
      state.projectZOOM = zoom
    },
    // Desde GaugeComponent
    setIEODistri (state, IEOdistri){
      state.ieoDistri = IEOdistri
    },
    // Desde GaugeComponent
    setIEOAcceso (state, IEOAcceso){
      state.ieoAcceso = IEOAcceso
    },
    // Desde GaugeComponent
    setIEOClient (state, IEOCliente){
      state.ieoClient = IEOCliente
    },
    // desde login, APPBar
    setUserSesion(state,value){
      state.sesion.user = value
    },
    // desde login
    setUserRol(state,value){
      state.sesion.rol = value
    },
    // Desde KPI-component
    setTotalKPI(state,value){
      state.arrayTotalKPI = value
    },
    // desde Drawer Bar
    postKPIValue(state,value){
      state.valueKPI = value
    },
    // Desde la Accion
    addListItems(state,value){
      state.arrayListItems = value
    },

    /**
     * Recibe el array de objetos con valores: 
     * name, tipo, subtipo, ip, marca, status
     * lo almacena en la STORE para usarse en otro momento
     * @param state 
     * @param value 
     */
    addAllItemsByStatus(state,value){
      state.arrayItemsByStatus = value
    },

    // Desde Varios
    showOverlay(state,value){
      state.overlayLoading = value
    },
    showSnackBar(state, snack){
        state.snackbar = snack
    },
    setSnackAdd(state, stateSnack){
      state.snackbarAdd = stateSnack
    },
    setSnackEdit(state, stateSnack){
      state.snackbarEdit = stateSnack
    },
    setSnackDelete(state, stateSnack){
      state.snackbarDelete = stateSnack
    },
    setSnackError(state, stateSnack){
      state.snackbarError = stateSnack
    },
    // Desde la Accion
    setSnackMSG(state, msg){
      state.snackbarmsg = msg
    },
    // desde AppBar
    showDrawer(state){
      state.drawer = !state.drawer
    },
    // desde Drawer Bar
    noShowDrawer(state){
      state.drawer = false
    },
    // desde Drawer Bar
    yesShowDrawer(state){
      state.drawer = true
    },
    
    /**
     * Muestra u oculta el componente "VirtualMachines" del Dashboard
     * dependiendo si encuentra o no valores desde la API en "VirMacDialog"
     * @param state estado de la STORE
     * @param value Boolean 
     */
    setVirtualMachine(state, value){
      state.showVirMac = value;
    },
  },
  actions: {
    // Enviado desde AdminView, AdminOIDSview, COPYCOmponent
    async getInvetoryData({commit}){
        await axios.get(CONSTAPIS.APIS.value.general.inventory, { timeout: 10000 })
        .then((response) =>{
          commit('addListItems', response.data.body)
        })
        .catch(function (error) {
      
          // Si no hay una ruta a la API (no esta definida)
          if (error.response) {
              console.error('no se ha encontrado la ruta a la API');
          }
      
          // Si la API tiene un error
          else if (error.request) {
              console.error('Error de la API o 10s sin recibir nada');
          } 
      
          // Otros motivos
          else {
              console.error('otro Motivo: ',error)
          }
      })
    },  

    //ENVIADO DESDE LASTCONECTION, YAMLMaintenance, DBMaintenance, Login, COPYCOmponent
    async showSnackBar({commit}, {type,msg}){
      commit(type, true);
      commit('setSnackMSG', msg);

      setTimeout(() => {
          commit(type, false);
          commit('setSnackMSG','');
      }, 4000);
    },
    
  },
  modules: {
  }
})
