watch(mdAndDown,(valor:boolean) => {
    valor ? width.value = '100%' :  width.value = '350';
})


const allInfoSelectedIP = ref({
    ip: '',
    latitud : 0,
    longitud: 0,
    bandwidth : { Tx:0, Rx:0},
    fecha: ''
})


const minutes = ref(1)
const interval = ref(15)
const disabledAXIOS = ref(false)

async function startBandWidth(){
    const params = {
        minutes: minutes.value,
        interval: interval.value
    }

    disabledAXIOS.value = true;

    const allData = await postBandWidth(params)

    disabledAXIOS.value = false

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        if(allData.data[0].includes('ERROR')){
            store.commit('showSnackBar', {type: LABELS.error, msg: allData.data[1], valor : true});
        }
        else{
            store.commit('showSnackBar', {type: LABELS.add, msg: t('snackbar.heatmapOK'), valor : true});
        }
    }
}

watch(disabledAXIOS,(a)=> {
    if(a) startTimer()
})


const progress = ref(0);
const isRunning = ref(false);

let totalSeconds = 0;
let elapsedSeconds = 0;
let intervalId: ReturnType<typeof setInterval> | null = null;

const startTimer = () => {
  if (!minutes.value || minutes.value <= 0) return;

  if (intervalId) {
    clearInterval(intervalId);
  }

  totalSeconds = minutes.value * 60;
  elapsedSeconds = 0;
  progress.value = 0;
  isRunning.value = true;

  intervalId = setInterval(() => {
    elapsedSeconds++;
    progress.value = (elapsedSeconds / totalSeconds) * 100;

    if (elapsedSeconds >= totalSeconds) {
      clearInterval(intervalId!);
      isRunning.value = false;
    }
  }, 1000);
};

/**
 * Get PMPAP Data
 */


const infoWindowVisible     = ref(false);

const getValueStoreDRIVE    = ref('drive');
const title                 = ref('Ancho de Banda');

const showFilters           = ref(true);

const ipSelected     = ref('');
const allListIPS     = ref([''])
const haulTrucksList:Ref<TOPO_ITEMS[]>      = ref([]);
const finalListTruck:Ref<TOPO_ITEMS[]>      = ref([]);

const selectedTX:Ref<boolean>         = ref(true);
const selectedRX:Ref<boolean>         = ref(false);
const showError                         = ref(false);
const showCheckBoxes                    = ref(false);

const COLORRED      = COLOR.red;
const COLORYELLOW   = COLOR.yellow;
const COLORGREEN    = COLOR.green;
const COLORBLUE     = COLOR.greyfake;
const COLORGREY     = COLOR.grey;

const getColorByValue = (value: number, limit:number[], colors:string[]) => {
    if( value === 0 ) return COLORGREY;
    let finalColor = colors;
    let selectedColor = COLORGREY;
    limit.forEach((limited,i) => {
        if(value > limited ) selectedColor = finalColor[i]
    })
    return selectedColor
}

const finalTXArray   = ref<DRIVE_PROPS[]>([]);
const finalTXObject   = ref<DRIVE_OBS>({'features': [], "type": ''});
const finalRXArray    = ref<DRIVE_PROPS[]>([]);
const finalRXObject   = ref<DRIVE_OBS>({'features': [], "type": ''});

const getFinalPositions = (lat:number, lng:number) => {
    let lat1 = lat + 0.0002;
    let lng1 = lng + 0.0002;
    let finalItem = [
        [ lng,  lat  ],
        [ lng1, lat  ],
        [ lng1, lat1 ],
        [ lng,  lat1 ],
        [ lng,  lat  ],
    ]
    return finalItem
}


const getRajantPositions = async() => {
    await getALLRajantGPS();
    await getALLRajantPerfomance();

    clearLayerSource('LayerTX','SourceTX');
    clearLayerSource('LayerRX','SourceRX');
    selectedTX.value = true;
    selectedRX.value = false;
    showError.value = false;
    showCheckBoxes.value = false;
    finalTXArray.value = [];
    
    const finalItem = allInfoSelectedIP.value;

    if(finalItem.bandwidth.Tx && finalItem.latitud !== 0) {
            
        let lat = finalItem.latitud;
        let lng = finalItem.longitud;

        let TX = finalItem.bandwidth.Tx;
        let RX = finalItem.bandwidth.Rx;

        let colorTX = getColorByValue(TX, [-Infinity,-20,-15,-10],[COLORRED,COLORYELLOW, COLORGREEN, COLORBLUE]);
        let colorRX = getColorByValue(RX, [-Infinity,-20,-15,-10],[COLORRED,COLORYELLOW, COLORGREEN, COLORBLUE]);

        postPORTItems('TX',lat, lng, colorTX, TX !== 0 ? `TX: ${TX}`: `Fuera de Linea`,1,finalItem.fecha);
        postPORTItems('RX',lat, lng, colorRX, RX !== 0 ? `RX: ${RX}`: `Fuera de Linea`,1,finalItem.fecha);
        

        finalTXObject.value = { "features" : finalTXArray.value, type : "FeatureCollection" };
        await setSourceAndLayer('TX','SourceTX',finalTXObject.value,'LayerTX');

        finalRXObject.value = { "features" : finalRXArray.value, type : "FeatureCollection" };
        await setSourceAndLayer('RX','SourceRX',finalRXObject.value,'LayerRX');

        clearLayerSource('LayerRX','SourceRX');
        clearLayerSource('LayerRSSI','SourceRSSI');
        showCheckBoxes.value = true;
    }
    else {
        showError.value = true;
        showCheckBoxes.value = false;
    }
}

const postPORTItems = (tipo:string, latitud:number, longitud:number, color:string, valor:string, index:number, fecha:string) => {
    let finalITEM = {                        
            "type": "Feature",
            "properties": {
                "level": 0,
                "name": `AllGraph${tipo}`,
                "height": 40,
                "base_height": 0,
                "color": color,
                "center": [longitud,latitud],
                "valor": valor,
                "fecha" : fecha
            },
            "geometry": {
                "coordinates": [ getFinalPositions(latitud,longitud) ],
                "type": "Polygon"
            },
            'id': index 
    }
    if(tipo.toUpperCase() === 'TX')       { finalTXArray.value.push(finalITEM) }
    if(tipo.toUpperCase() === 'RX')       { finalRXArray.value.push(finalITEM) }
}

const setSourceAndLayer = async (tipo:string, source:string, alldata:DRIVE_OBS, layer:string) => {
    const mySource = map.getSource(source);
    if(mySource === undefined){ 
        map.addSource(source, { 'type': 'geojson', 'data': alldata});        
        map.addLayer({
            'id': layer,
            'type': 'fill-extrusion',
            'source': source,
            'paint': {
                'fill-extrusion-color': ['get', 'color'],
                'fill-extrusion-height': ['get', 'height'],
                'fill-extrusion-base': ['get', 'base_height'],
                'fill-extrusion-opacity':1,
            }
        });
        
        const popup = new maplibregl.Popup({
            closeButton: false,
            closeOnClick: false
        });

        map.on('mouseenter', layer, (e:any) => {
            map.getCanvas().style.cursor = 'pointer';
            
            const coordinates:any = [e.lngLat.lng, e.lngLat.lat];
            const valor = e.features[0].properties.valor ;
            let mensaje = `${valor}` 

            while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
            }

            popup.setLngLat(coordinates).setHTML(mensaje).addTo(map);
        });

        map.on('mouseleave', layer, () => {
            map.getCanvas().style.cursor = '';
            popup.remove();
        });
    }
    else{
        mySource.setData(alldata)
    }
}


watch(getValueStoreDRIVE,(a:string, b:string)=>{
    a !== b ? resetData() : null;
    finalListTruck.value = haulTrucksList.value;
})

const clearLayerSource = (layer:string, source:string) => {
    if(map.getLayer(layer)) { map.removeLayer(layer); }
    if(map.getSource(source)) { map.removeSource(source); }
}

const watchLayer = (selectedVar: Ref<boolean>, type: string, source: string, finalObject: Ref<DRIVE_OBS>, layer: string) => {
    watch(selectedVar, (state) => {
        if (state) {
            setSourceAndLayer(type, source, finalObject.value, layer);
        } else {
            clearLayerSource(layer, source);
        }
    });
}; 

watchLayer(selectedTX, 'TX', 'SourceTX', finalTXObject, 'LayerTX');
watchLayer(selectedRX, 'RX', 'SourceRX', finalRXObject, 'LayerRX');

const resetData = () => {
    ipSelected.value = '';
    infoWindowVisible.value = false;
}

async function getALLRajantIPS(){

    disabledAXIOS.value = true;

    const allData = await getRajantIPS()

    disabledAXIOS.value = false

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        allListIPS.value = allData.data
    }
}

async function getALLRajantGPS(){

    const params = { ip : ipSelected.value }

    const allData = await getRajantGPS(params)

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        allInfoSelectedIP.value.ip = allData.data[0].ip;
        allInfoSelectedIP.value.latitud = allData.data[0].latitud;
        allInfoSelectedIP.value.longitud = allData.data[0].longitud;
    }
}

async function getALLRajantPerfomance(){

    const params = { ip : ipSelected.value }

    const allData = await getRajantPerfomance(params)

    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        allInfoSelectedIP.value.bandwidth = JSON.parse(allData.data[0].bandwidth);
        allInfoSelectedIP.value.fecha = allData.data[0].fecha;
    }
}


let map:any = '' ;
const centerMap = ref<LngLatLike>([CONSTMAPS.longitud.value,CONSTMAPS.latitud.value])

onMounted(async() => {    
    getALLRajantIPS();
    map  = new maplibregl.Map({
        container: 'map',
        center: centerMap.value,
        zoom: 14,
        pitch: 40,
        bearing: 30,
        antialias: true,
        style: 'https://api.maptiler.com/maps/satellite/style.json?key=6JWVt6LFaY22nquimZpF',
    });
    
    map.addControl(new maplibregl.NavigationControl());

})


