<template>
<div v-if="showTotalItems">
    <v-sheet  color="bgtopology" class="defaultCard" >
        <v-row class="no-gutters" justify="center">
            <v-col cols="12" md="6" lg="4" v-for="(item,i) in finalItemsKPI" :key="i">
                <span class="titleDashboard">{{ t(`labels.${item.valor}`) }}</span>
                <v-row class="mt-4">
                    <v-col cols="12">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.ok_pl')" :icon="'mdi-check'" :color-icon="COLOR.green" :middleText="item.count.ok.toString()"/>
                    </v-col>
                    <v-col cols="12">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.alarm_pl')" :icon="'mdi-lightbulb-on-outline'" :color-icon="COLOR.yellow" :middleText="item.count.alarm.toString()"/>
                    </v-col>
                    <v-col cols="12">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.alert_pl')" :icon="'mdi-exclamation-thick'" :color-icon="COLOR.red" :middleText="item.count.alert.toString()"/>
                    </v-col>
                    <v-col cols="12">
                        <SingleCard class="cardSingleKPI" @click="goToTopology(item.valor)" :heigth="height" :bottomText="t('labels.down_pl')" :icon="'mdi-close'" :color-icon="COLOR.greyfake" :middleText="item.count.down.toString()"/>
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <v-sheet color="transparent" :width="smAndDown ? 500 : 395" height="460">
                        <v-col class="pa-0" style="height: 460px; align-content: center;">                    
                            <GaugeComponent
                            :recharge="false"
                            :array-items="item.total"
                            :valorCard ="item.valor"
                            />
                        </v-col>
                    </v-sheet>
                </v-row>
                <v-row justify="center">
                    <v-sheet color="transparent" :width=" width" :class="mdAndDown ? 'pb-3' : 'pr-2'">
                        <div>
                            {{ t('labels.msn') }}
                        </div>
                        <TimeLineAlerts
                        v-if="item.alarm.length > 0"
                        :array-items="item.alarm"
                        :width="400"
                        :height="height"
                        />
                        <p class="mt-8" v-else>
                            <span class="textDashboard" > {{ t('noAlertsAlarm') }} </span>  
                        </p>
                    </v-sheet>
                </v-row>
            </v-col>
        </v-row>
    </v-sheet>
</div>

<v-sheet v-else color="bgtopology" class="defaultCard text-center">
    <Prog_circular />
</v-sheet>

</template>

<script setup lang="ts">
import {  Ref, ref, computed, onMounted } from 'vue';
import router               from '@/router';
import { useI18n }          from 'vue-i18n';
import { useDisplay }       from 'vuetify';
import { useStore }         from 'vuex';
import { PropType }         from 'vue';

// CONSTANTS
import { LABELS, STRING_GENERAL } from '@/constants/labels';
import { COLOR }          from '@/constants/colors';

// COMPONENTS
import GaugeComponent       from '@/components/SmartLink/dashboard/GaugeComponent.vue';
import TimeLineAlerts       from '@/components/SmartLink/dashboard/TimeLineAlerts.vue';
import Prog_circular        from '@/components/PrincipalComponents/Prog_circular.vue';
import SingleCard           from '@/components/SmartLink/topology/SingleCard.vue';


// INTERFACES
import { getAlarmDashboard, getAllOperability } from '@/services/Smartlink/Principal';
import { KPI_COMP, OBJ_ALARM_TYPE, OBJ_TYPE_STATUS } from '@/interfaces/SmartLink/Equipos';

const { t } = useI18n();

const props = defineProps({
    recharge            : { type: Boolean, required: true },
    countEquipStatus    : { type: Object as PropType<OBJ_TYPE_STATUS>, required: true },
})

const { mdAndDown, smAndDown }  = useDisplay();
const width     = ref('425');
const height    = ref(160);

/**
 * Objeto con la cantidad de estados según el subtipo
 */
const finalCount = computed<OBJ_TYPE_STATUS>(() => props.countEquipStatus);

/**
 * Objeto que alamcenará toda la información de la vista
 */
const finalItemsKPI:Ref<KPI_COMP> = ref({
    distri: { valor:STRING_GENERAL.distri, alarm: [], total: [], count: finalCount.value.distri, showError: false },
    acceso: { valor:STRING_GENERAL.access, alarm: [], total: [], count: finalCount.value.acceso, showError: false },
    client: { valor:STRING_GENERAL.client, alarm: [], total: [], count: finalCount.value.client, showError: false },
})


const arrayAlarmItems = ref<OBJ_ALARM_TYPE>({
    distri  : [],
    acceso  : [],
    client  : [],
})

/**
 * Captura los valores de la API: "getAlarmToDashBoard" y la divide por Subtipo
 */
const getAlarmToDashBoard = async () => {
    const allData = await getAlarmDashboard()
    
    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        arrayAlarmItems.value = allData.data
    }
}


const arrayOperabilityItems = ref({
    distri  : [],
    acceso  : [],
    client  : [],
})

/**
 * Captura los valores de la API: "getAllOperability" y la divide por Subtipo
 */
const getAllOperabilityData = async() => {

    const allData = await getAllOperability()
    
    if( allData.status === 400 ){
        store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
    }
    if( allData.status === 200 ){
        arrayOperabilityItems.value = allData.data
    }
}


/**
 * Muestra el componente entero una vez se haya cargado toda la información
 */
const showTotalItems                    = ref(false);

/**
 * Se coloca todo lo anterior hecho, tanto el total como las alarmas
 * dentro del mismo objeto para usarlo en un "v-for" en el template
 */
const matchItems = async () => {
    finalItemsKPI.value.distri.total        = arrayOperabilityItems.value.distri;
    finalItemsKPI.value.distri.alarm        = arrayAlarmItems.value.distri;
    finalItemsKPI.value.acceso.total        = arrayOperabilityItems.value.acceso;
    finalItemsKPI.value.acceso.alarm        = arrayAlarmItems.value.acceso;
    finalItemsKPI.value.client.total        = arrayOperabilityItems.value.client;
    finalItemsKPI.value.client.alarm        = arrayAlarmItems.value.client;
    showTotalItems.value                    = true;
}

/**
 * Cuando se le da click a alguno de los números, redirecciona a la vista
 * de topología de red, desplazando hasta el subtipo especificado
 * @param viewPage Subtipo de los equipos
 */
const goToTopology = (viewPage:string) => {
    router.push(`/smartlink/topology#${viewPage}`)
}

const store     = useStore();

onMounted(async () => {
    getAlarmToDashBoard();
    await getAllOperabilityData();
    await matchItems();

    /**
     * Almacena en el valor de la store "arrayTotalKPI", todo el objeto obtenido aquí
     * para luego ser usado en "TopologyStatsView"
     */
    store.commit('setTotalKPI',finalItemsKPI.value)
})

</script>