import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'

import LoginViewVue           from '@/views/PrincipalHome/LoginView.vue';
import PageDown               from '@/views/PrincipalHome/PageDownView.vue';
import VoidPageView           from '@/views/PrincipalHome/VoidPageView.vue';
import PrincipalPageView      from '@/views/PrincipalHome/PrincipalPageView.vue';
import DashBoardView          from '@/views/SmartLink/Dashboard/DashBoardView.vue';
import TopologyStatsView      from '@/views/SmartLink/Topo - Insta/TopologyStatsView.vue';
// import AdminCSVView           from '@/views/SmartLink/Admin/AdminCSVView.vue';
import AdminView              from '@/views/SmartLink/Admin/AdminView.vue';
import DbStatus               from '@/views/SmartLink/Admin/DBstatusview.vue';
import DBMaintenance          from '@/views/SmartLink/Admin/DBmaintenance.vue';
import YAMLMaintenance        from '@/views/SmartLink/Admin/YAMLMaintenance.vue';
import KPIView                from '@/views/SmartLink/KPI/KPIView.vue';
import AllLatency             from '@/views/SmartLink/KPI/AllLatencyView.vue';
import PredictView            from '@/views/SmartLink/IA/PredictView.vue'
import PredictSNR             from '@/views/SmartLink/IA/PredictSNRView.vue'
import ServerView             from '@/views/SmartLink/Server - LTE/ServerView.vue';
import ReportView             from '@/views/SmartLink/Report/ReportView.vue';
import Drive                  from '@/views/SmartLink/Drive/DriveView.vue';
import DriveRAJANT            from '@/views/SmartLink/Drive/DriveRajantView.vue';
import BandWidth              from '@/views/SmartLink/Drive/WidthBandView.vue';
import TestEO                 from '@/views/SmartLink/Test/Test-EO.vue';
import ColorSelectView        from '@/views/PrincipalHome/ColorSelectView.vue';
import MonitorinView          from '@/views/GestorHCGLink/MonitoringView.vue';
import ConfigurationView      from '@/views/GestorHCGLink/Configurations/ConfigurationView.vue';
import StatisticsView         from '@/views/GestorHCGLink/StatisticsView.vue';
import ResetView              from '@/views/SmartLink/Reset/ResetView.vue';
import { VISIBLE_PAGES } from '@/constants/labels';

const routes: Array<RouteRecordRaw> = [  
  {
    path: '/',
    name: 'Inicio',
    component: VoidPageView
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginViewVue
  },
  {
    path: '/principalPage',
    name: 'Home',
    component: PrincipalPageView
  },
  
  { path: `/smartlink/${VISIBLE_PAGES.report}`,           name: 'SmartLink-Report',         component: ReportView },
  { path: `/smartlink/${VISIBLE_PAGES.servers}`,          name: 'SmartLink-Servers',        component: ServerView },
  { path: `/smartlink/${VISIBLE_PAGES.topology}`,         name: 'SmartLink-Topology',       component: TopologyStatsView },
  { path: `/smartlink/${VISIBLE_PAGES.predicts}`,         name: 'SmartLink-PredictRX',      component: PredictView },
  // { path: `/smartlink/${VISIBLE_PAGES.admincsv}`,         name: 'SmartLink-AdminCSV',       component: AdminCSVView },
  { path: `/smartlink/${VISIBLE_PAGES.dbstatus}`,         name: 'SmartLink-DBstatus',       component: DbStatus },
  { path: `/smartlink/${VISIBLE_PAGES.dashboard}`,        name: 'SmartLink-Dashboard',      component: DashBoardView },
  { path: `/smartlink/${VISIBLE_PAGES.allLatency}`,       name: 'SmartLink-AllLatency',     component: AllLatency },
  { path: `/smartlink/${VISIBLE_PAGES.management}`,       name: 'SmartLink-Admin',          component: AdminView },
  { path: `/smartlink/${VISIBLE_PAGES.predictsnr}`,       name: 'SmartLink-PredictSNR',     component: PredictSNR },
  { path: `/smartlink/${VISIBLE_PAGES.dbmaintenance}`,    name: 'SmartLink-DbMaintena',     component: DBMaintenance },
  { path: `/smartlink/${VISIBLE_PAGES.YAMLMaintenance}`,  name: 'SmartLink-YMLMaintena',    component: YAMLMaintenance },

  
  { path: `/smartlink/${VISIBLE_PAGES.drive}`,            name: 'SmartLink-Drive',          component: Drive },
  { path: `/smartlink/${VISIBLE_PAGES.driverajant}`,      name: 'SmartLink-DriveRajant',    component: DriveRAJANT },
  { path: `/smartlink/${VISIBLE_PAGES.bandWidth}`,        name: 'SmartLink-BandWidth',      component: BandWidth },


  { path: `/smartlink/${VISIBLE_PAGES.quality_lap}`,      name: 'SmartLink-Quality_lap',    component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.quality_snr}`,      name: 'SmartLink-Quality_snr',    component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.wiredpeers}`,       name: 'SmartLink-Wiredpeers',     component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.latency}`,          name: 'SmartLink-Latency',        component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.wirelesspeers}`,    name: 'SmartLink-Wirelesspeers',  component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.temp_inst}`,        name: 'SmartLink-Temp_inst',      component: KPIView },
  { path: `/smartlink/${VISIBLE_PAGES.operability}`,      name: 'SmartLink-Operability',    component: KPIView },
  
  
  { path: `/smartlink/${VISIBLE_PAGES.monitoring}`,      name: 'SmartLink-Monitoring',    component: MonitorinView },
  { path: `/smartlink/${VISIBLE_PAGES.config}`,          name: 'SmartLink-Configuration', component: ConfigurationView },
  { path: `/smartlink/${VISIBLE_PAGES.statistics}`,      name: 'SmartLink-Statistics',    component: StatisticsView },
  { path: `/smartlink/${VISIBLE_PAGES.reset}`,                            name: 'SmartLink-Reset',         component: ResetView },



  {
    path: '/maintenance',
    name: 'PageDown',
    component: PageDown
  },
  
  { path: `/smartlink/test`,              name: 'SmartLink-TEST',            component: TestEO,},
  { path: `/smartlink/colors`,            name: 'SmartLink-Color',            component: ColorSelectView,},
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  // Verificar si la ruta solicitada existe en las rutas definidas
  const exists = routes.some(route => route.path === to.path);
  if (!exists) {
    // Si la ruta no existe, redirigir al menú principal
    next(`/smartlink/${VISIBLE_PAGES.dashboard}`);

    // next(`/smartlink/test`);

    // next(`/principalPage`);
  } else {
    // Si la ruta existe, continuar con la navegación normalmente
    next();
  }
});



export default router
