<template>
    <div  :style="styles" class="contenedorNANO">
        <v-sheet class="hexagonNANO"/>
        <!-- <v-sheet class="subtituloNANO text-center" width="70" color="transparent">
            <span class="textDashboard text-white" >  {{ subtitle }} </span>
        </v-sheet> -->
        <span class="tituloNANO"> {{ title }} </span>
    </div>
    
</template>

<script setup lang="ts">
import { computed, ref, onMounted, watch } from 'vue';
const props = defineProps({
    title       : { type: String, required: true },
})

const finalNumber = computed(() => props.title)
const finalColor = ref('')

const getColorByNumber = () => {
    let number = parseFloat(finalNumber.value);
    if (number >= 0.9) finalColor.value = '#04CB04'
    if (number < 0.9 && number >= 0.6 ) finalColor.value = '#D1D10D'
    if (number < 0.6) finalColor.value = '#FF0000'
}

watch(finalNumber,() => {
    getColorByNumber
} )

        // const finalColor = computed(() => props.color)

const styles = computed(() =>  ({
    '--varColor' : finalColor.value,
}))

onMounted(() => {
    getColorByNumber()
})

</script>

<style scoped>

.contenedorNANO{
    position: relative;
    width: calc(25px * 0.645);
    height: 25px;
    margin: 5px 10px 2px;
}

.contenedorNANO .hexagonNANO {
  cursor: pointer;
  position: absolute;
  width: calc(25px * 0.645);
  height: 25px;
  background-image: linear-gradient(90deg, var(--varColor),var(--varColor));
  border-radius: calc(25px / 10);
  transition: transform 400ms ease;
  
}

.contenedorNANO:hover .hexagonNANO{
    transform: rotate(30deg);
}

.contenedorNANO .hexagonNANO:before, .contenedorNANO .hexagonNANO:after{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: inherit;
    height: inherit;
    background-image: inherit;
    border-radius: inherit;
}

.contenedorNANO .hexagonNANO:before{
    transform: rotate(60deg);
}

.contenedorNANO .hexagonNANO:after{
    transform: rotate(-60deg);
}

.contenedorNANO  .tituloNANO{
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    font-family: 'Poppins',sans-serif;
    font-weight: bold;
    font-size: 12px;
    text-shadow: 2px 3px 3px rgba(0,0,0,0.2);
    color: white;
}

.contenedorNANO  .subtituloNANO{
    position: absolute;
    z-index: 2;
    top: 25%;
    left: 5%;
    /* transform: translate(-60%, -50%); */
    cursor: pointer;
    font-family: 'Poppins',sans-serif;
    font-weight: 400;
    font-size: 15px;
    color: white;
}



</style>