            let statusHijo = allFinalItems.value[item.ip_cliente] ? allFinalItems.value[item.ip_cliente].status : 'down'
            
            let pesoPadre = getPesos(statusPadre);
            let pesoHijo = getPesos(statusHijo);

            let finalPeso = pesoPadre + pesoHijo;
            let finalStatus = 'DOWN'
            if(finalPeso >= 0 && finalPeso <= 2) finalStatus = 'OK';
            if(finalPeso >= 3 && finalPeso <= 4) finalStatus = 'ALARM';
            if(finalPeso >= 5 && finalPeso <= 6) finalStatus = 'ALERT';
            if(finalPeso >= 7 && finalPeso <= 8) finalStatus = 'DOWN';

            let finalposition = [positionPadre,positionHijo]
            polilynes.value.push(optionsRect(finalStatus,finalposition));
        })
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const showPolylines = async (isShow:boolean, isOnlyAlert:boolean) => {
    if(!isShow){
        finalPolilynes.value    = [];
        showPMPSM.value     = false;
    }
    else{
        let prePolylines:any[] = [];
        polilynes.value.forEach((polilyne:any)=> {
            if(isOnlyAlert){
                polilyne.colorLine.toUpperCase() !== 'GREEN' ? prePolylines.push(polilyne) : null     
            }else{
                prePolylines.push(polilyne)
                showPMPSM.value = true;
            }
        })
        finalPolilynes.value = prePolylines;
    }
}

const getLatLongRAJANT = async () => {
    await axios.get(CONSTAPIS.APIS.value.maps.getAllLatLngRAJ, { timeout : 10000 }).then((response)=>{
        elementRAJANT.value = response.data;

        elementRAJANT.value.forEach((element:EQUIPAMENTS) => {

            const latitud   = element.latitud;
            const longitud  = element.longitud; 
            let position    = [latitud,longitud]; 

            markersrajant.value.push({
                position    : position,
                label       : element.name,
                name        : element.name,
                ip          : element.ip,
                tipo        : element.tipo,
                status      : getStatusEquipment(element.ip??''),
            })
        })           
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const getStatusEquipment = (ipstring:string) => {
    let finalStatusEquipment = 'ok';

    statusMarkers.value.find(({ip,status}:any) => {
        if(ipstring === ip) finalStatusEquipment = status
    });

    return finalStatusEquipment;
}

/**
 * Get PMPAP Data
 */
const getLatLongPMPAP = async () => {
    await axios.get(CONSTAPIS.APIS.value.maps.getAllLatLngAP, { timeout : 10000 }).then((response)=>{
            
        let itemsLatLng = response.data;
        itemsLatLng.forEach((element:EQUIPAMENTS) => {
            let finalLatitud     = 0;
            let finalLongitud    = 0;
            let finalPosition    = [finalLatitud, finalLongitud];
            let equipName   = element.name;
            let anotacion   = element.anotacion !== null ? JSON.parse(element.anotacion??'{}') : {};
            if(anotacion.gps){
                markersrajant.value.find(({ip,position}) => {
                    if(anotacion.gps == ip) {
                        finalPosition = position
                    }
                })
            }
            else {
                let gps = element.gps !== null ? JSON.parse(element.gps??'{"latitud":0, "longitud":0}') : {latitud:0, longitud:0}
                finalLatitud = gps.latitud;
                finalLongitud = gps.longitud;
                if(element.name?.includes( STRING_GENERAL.PMPAP1 ) || 
                element.name?.includes( STRING_GENERAL.PMPAP5 ) ){
                    finalLatitud    = element.longitud??0 + 0.000512,
                    finalLongitud   = element.latitud??0 - 0.000512
                }
                finalPosition = [finalLatitud, finalLongitud]

                if(element.ip !== undefined){
                    toPMPAP.value[element.ip] = finalPosition;
                }
            }
            

            markerspmpap.value.push({
                position: finalPosition,
                name    : `${equipName}`,
                ip      : element.ip,
                tipo    : element.tipo,
                status  : getStatusEquipment(element.ip??''),
            })
            
        })
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const haulTrucksList:Ref<TOPO_ITEMS[]>  = ref([]);
const haulTruckSelected:any    = ref();

const getAllHaulTrucks = async() => {
    await axios.get(CONSTAPIS.APIS.value.maps.getAllTrucks, { timeout : 10000 }).then((response)=>{
        let finalItems = response.data;
        finalItems.forEach((item:EQUIPAMENTS) => {
            haulTrucksList.value.push({
                title   : item.name??'',
                value   : item.ip??''
            })
        })
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const circles:Ref<any>             = ref([]);
const totalGPSHaulTruck:Ref<any>             = ref([]);
const allCircles:Ref<any[]>          = ref([]);
const allCirclesFiltered:Ref<CIRCLE[]>  = ref([]);
const showError                         = ref(false);
const arrayDatesCircle:Ref<string[]>    = ref([]);
const modelSliderStatus:Ref<number[]>   = ref([0,12]);
const disableSearchTruck                = ref(true);

const getHaulTruckPositions = async() => {
    showError.value         = false;
    showFiltersStatus.value = false;
    circles.value           = [];
    totalGPSHaulTruck.value = [];

    let params = {
        ip : haulTruckSelected.value
    }

    await axios.get(CONSTAPIS.APIS.value.maps.getPositTrucks,{ params : params, timeout : 10000 }).then(async(response)=>{
    
        allCircles.value            = [];
        allCirclesFiltered.value    = [];
        modelSliderStatus.value     = [0,12];

        let finalItems = response.data;
        if(finalItems.length > 0) {
            disableSearchTruck.value = true;

            finalItems.forEach((item:EQUIPAMENTS) => {
                let lat     =  0
                let lng     =  0
                    lat         = item.latitud??0;                
                    lng         = item.longitud??0;
                
                let preImg      = '';
                let laten       = item.lat??0;
                let color       = getColorByLatency(laten);
                if(laten > 0 && laten <= 100)   preImg = require("@/assets/Img/Green/cliente.png")
                if(laten > 100 && laten <= 200) preImg = require("@/assets/Img/Yellow/cliente.png")
                if(laten > 200 && laten <= 500) preImg = require("@/assets/Img/Red/cliente.png")
                if(laten > 500 || laten <= 0)   preImg = require("@/assets/Img/Grey/cliente.png")

                allCircles.value.push({
                    fecha   : item.fecha??'',
                    position: [lat,lng],
                    ip      : haulTruckSelected.value,
                    tipo    : 'Haultruck',
                    img     : preImg,
                    color   : color 
                })
            })
        }
        else {
            showError.value = true;
            showFiltersStatus.value = false;
            disableSearchTruck.value = true
        }
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}


watch(modelSliderStatus,(a:number[], b:number[])=>{
    if(JSON.stringify(a)!==JSON.stringify(b)) showCircles(arrayDatesCircle.value[a[0]],arrayDatesCircle.value[b[1]])
})

const showCircles = async (firstDate:string, lastDate:string) => {
    circles.value = [];
    allCirclesFiltered.value.forEach((circle:CIRCLE) => {
        if(circle.fecha && new Date(circle.fecha).getTime() <= new Date(lastDate).getTime() && new Date(circle.fecha).getTime() >= new Date(firstDate).getTime()){
            circles.value.push(circle)
        }
    })
}

const getColorByLatency = (latency:number) => {
    let finalcolor = '';
    if(latency === -1)                      finalcolor = 'grey';
    if(latency >= 0     && latency <= 100)  finalcolor = 'green';
    if(latency >= 100   && latency <= 200)  finalcolor = 'yellow';
    if(latency >= 200   && latency <= 500)  finalcolor = 'red';
    return finalcolor
}

/**
 * When click in Item, show Dialog Component
 */
const showDialog = (ip:string, type:string, name:string, status:string) =>{
    dialog.value            = !dialog.value;
    selectedName.value  = name;
    selectedIp.value        = ip;
    selectedType.value      = type;
    finalStatus.value       = status;
}

const closeDialog = (value:boolean) => {
    dialog.value            = value
}

const latAlarm:Ref<any>              = ref([]);
const latAlert:Ref<any>              = ref([]);
const totalClusteringData:Ref<CLUSTERING[]> = ref([]);
const valueSliderMap                        = ref(12);
const arrayDatesClustering:Ref<string[]>    = ref([])


const get12HoursByClustering = async () => {
    await axios.get(CONSTAPIS.APIS.value.ia.get12HourCluster, { timeout : 10000 }).then((resp) => {
        let arrayFechas = resp.data;
        arrayFechas.forEach((item:CLUSTERING) => {
            arrayDatesClustering.value.push(item.fecha??'01/01/1900') 
        })
        arrayDatesClustering.value.sort()
    }) 
    .catch(function (error) {
        showConsoleError(error);
    });
}

const getClusteringMap = async () => {

    let params = {
        fecha : arrayDatesClustering.value[0]
    }

    await axios.get(CONSTAPIS.APIS.value.ia.getClusterData,{ params : params, timeout : 10000}).then((resp) => {
        totalClusteringData.value = resp.data;
        
        if(totalClusteringData.value.length > 0){
            getFilterByHours(arrayDatesClustering.value[arrayDatesClustering.value.length - 1]);
            showClusterChBox.value = false;
        }
        else{
            showClusterChBox.value = true;
        }
    })
    .catch(function (error) {
        showConsoleError(error);
    });
}

const getFilterByHours = (fechas:string) => {
    let arrayTotal          = totalClusteringData.value;
    let finalArray:CLUSTERING[]    = [];
    arrayTotal.forEach((element:CLUSTERING)=>{
        if(element.fecha && new Date(element.fecha).getTime() == new Date(fechas).getTime()){
            finalArray.push(element)
        }
    })
    getClusteringByHours(finalArray);
}

const getClusteringByHours = (arrayProv:CLUSTERING[]) => {
    latAlarm.value               = [];
    latAlert.value               = [];
    let princAlert:CLUSTERING[]  = [];
    let princAlarm:CLUSTERING[]  = [];

    arrayProv.forEach((item:CLUSTERING) => {
        if(item.label === 3) {
            princAlert.push(item)
            let latitud         = item.latitud??0;
            let longitud        = item.longitud??0;
            latAlert.value.push({
                position: [latitud, longitud]
            })
        }
        if(item.label === 4 ) {
            princAlarm.push(item)
            let latitud         = item.latitud??0;
            let longitud        = item.longitud??0;
            latAlarm.value.push({
                position: [latitud, longitud]
            })
        }
    })
}

const season = (val:number) => {
    let finalDate = arrayDatesClustering.value[val-1];
    return finalDate;
}

const addHours = () =>{
    if( valueSliderMap.value < 12) valueSliderMap.value ++;
}

const deleteHours = () =>{
    if( valueSliderMap.value > 1) valueSliderMap.value --;  
}

const hideALL = () => {
    showOnlyPMPAP.value     = false;    
    showOnlyPMPSM.value     = false;     showRajantwoPMPSMwoPMPAP.value = false;  
    showOnlyRajant.value    = false;   

    showSMandRajant.value   = false;     showRajantwoPMPSM.value = false;
    showSMandAP.value       = false;     showRajantwoPMPAP.value = false;
    showAPandRajant.value   = false;     showPMPSMwoRajant.value = false;
    showAPandSM.value       = false;     showPMPSMwoPMPAP.value  = false;
    showRajantandAP.value   = false;     showPMPAPwoPMPSM.value  = false;
    showRajantandSM.value   = false;     showPMPAPwoRajant.value = false;    
}


const showMarkerFunction = async (showitems:string) => {

    let functOBJ = showitems === '' ? 'hideALL' : showitems;

    const functionsItems:any = {
        'SM'        : () => { 
            hideALL();
            showOnlyPMPSM.value     = true;
        },
        'AP'        : () => {
            hideALL();
            showOnlyPMPAP.value     = true;
        },
        'RAJ'       : () => {
            hideALL();
            showOnlyRajant.value    = true;
        },
        'SMAP'      : () => {
            hideALL();
            showSMandAP.value       = true;
            showPMPSMwoPMPAP.value  = true;
            showPMPAPwoPMPSM.value  = true;
        },
        'APRAJ'     : () => {
            hideALL();
            showAPandRajant.value   = true;
            showRajantwoPMPAP.value = true;
            showPMPAPwoRajant.value = true;
        },
        'SMRAJ'     : () => {
            hideALL();
            showSMandRajant.value   = true;
            showPMPSMwoRajant.value = true;
            showRajantwoPMPSM.value = true;
        },
        'SMAPRAJ'   : () => {
            hideALL();
            showSMandRajant.value   = true;
            showSMandAP.value       = true;
            showPMPSMwoRajant.value = true;
            showAPandRajant.value   = true;
            showPMPAPwoRajant.value = true;
            showRajantwoPMPSMwoPMPAP.value = true;
        },
        'hideALL'   : () => {
            hideALL()
            showPolylines(true,true);
        }
    }
    functionsItems[functOBJ]()

}


watch(valueSliderMap, (v:number) => {
    let dateSelected = arrayDatesClustering.value[v-1]
    getFilterByHours(dateSelected)
})


