<template>
    <v-sheet class="defaultCard" color="bgtopology">
        <v-row justify="space-between" class="ma-0 mb-4">

            <!-- TITLE VIEW -->
            <v-col class="pa-0" cols="12" sm="4" md="7" lg="7" xl='8' align-self="center" >                
                <span  class="titlePages">{{ title }}</span>
            </v-col>
             
            <!-- SEARCH FIELD -->
            <v-col class="pa-0" cols="12" sm="8" md="5" lg="5" xl='4' style="text-align: -webkit-right;" >
                <v-text-field
                    v-if="items.length !== 0"
                    :style="!smAndDown ? 'max-width: 400px !important; margin-top: 20px':' margin-top: 10px'"             
                    v-model="searchValue"
                    :label="t('search')"
                    variant="outlined"
                    density="compact" 
                    hide-details 
                ></v-text-field>
            </v-col>
        </v-row>
        <v-row v-if="items.length !== 0" class="my-4">
            <v-col v-for="(item,i ) in finalLegend" :key="i">
                <rangeComponent
                    :principal-color="item.prinColor "
                    :secundary-color="item.secuColor "
                    :color-text-princ="item.colorText1"
                    :color-text-secun="item.colorText2"
                    :principal-text="item.prinText"
                    :secundary-text="item.secuText"
                    />
            </v-col>
        </v-row>

        <!-- TABLE COMPONENT -->
        <EasyDataTable
        v-if="items.length !== 0"
        table-class-name="customize-table"
        :search-field="searchField"
        :search-value="searchValue"
        :headers="finalHeader"
        :items="items"
        :class="finalLegend === null ? 'pt-10': ''"
        show-index
        @click-row="showDialog"
        >       
            <template #item-link_radio_rx ="item">
                <v-row style="align-items: center !important">
                    <span> {{ item.link_radio_rx }} </span>
                    <v-col cols="10">
                        <v-progress-linear
                        :model-value="-(item.link_radio_rx)"
                        height="10"
                        striped
                        :color="item.link_radio_rx >= -70 ? COLOR.green : 
                        item.link_radio_rx < -70 && item.link_radio_rx >= -80 ? COLOR.yellow : COLOR.red "
                        
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-vertical ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.vertical }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-2">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.vertical <= 15 ? '#FF0000' : item.vertical <= 20 ? '#FFEE00' :'#00FF00'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-horizontal ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.horizontal }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.horizontal <= 15 ? '#FF0000' : item.horizontal <= 20 ? '#FFEE00' :'#00FF00'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-snr_h ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.snr_h }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.snr_h <= 15 ? COLOR.red : item.snr_h <= 20 ? COLOR.yellow : COLOR.green"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-snr_v ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.snr_v }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.snr_v <= 15 ? COLOR.red : item.snr_v <= 20 ? COLOR.yellow : COLOR.green"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>

            <template #item-ok ="item">
                {{item.ok}}%
            </template>
            <template #item-alarm ="item">
                {{item.alarm}}%
            </template>
            <template #item-alert ="item">
                {{item.alert}}%
            </template>
            <template #item-down ="item">
                {{item.down}}%
            </template>
            <template #item-latencia ="item">
                <v-row justify="space-between" style="align-items: center !important">
                    <v-col cols="2" class="pa-0"><span> {{ item.latencia }} </span></v-col>
                    <v-col cols="10" class="pa-0 pr-6">
                        <v-progress-linear
                        style="border-radius: 20px;"
                        :model-value="100"
                        height="10"
                        striped
                        :color="
                        item.latencia < 0 ? '#9E9E9E' : item.latencia <= 100 ? '#00FF00' : item.latencia <= 500 ? '#FFEE00' :'#FF0000'"
                    ></v-progress-linear>
                    </v-col>
                </v-row>
            </template>
        </EasyDataTable>

        <!-- DIALOG COMPONENT -->
        <v-dialog v-model="dialog" width="85%">
            <DialogComponent
            v-if="dialog"
            :ip="selectedIp"
            :typeSel="selectedType"
            :name="selectedName"
            @dialog="closeDialog"
            :status="'Ok'"
            />             
        </v-dialog>

        <v-row  v-if="showError" class="ma-0 px-6 py-3">
            <h3 v-if="getValueStoreKPI === 'latency'"> {{t('noLatProbl')}} </h3>
            <h3 v-else-if="getValueStoreKPI === 'temp_inst'"> {{t('noTemProbl')}} </h3>
            <h3 v-else> {{t('noInformation')}} </h3>
        </v-row>

    </v-sheet>

</template>

<script setup lang="ts">
import { computed, onMounted, ref, watch, Ref, ComputedRef  } from 'vue'
import { useStore }                 from 'vuex';
import { useDisplay }               from 'vuetify';
import { useI18n }                  from 'vue-i18n';

// COMPONENTS
import DialogComponent              from '@/components/SmartLink/dialog/dialogComponent.vue';
import rangeComponent from '@/components/SmartLink/dashboard/rangeComponent.vue';


// CONSTANTS
import { COLOR }                  from '@/constants/colors';
import { FINAL_TYPE_LEGEND }        from '@/constants/legends';
import { FINAL_HEADERS_TABLE }  from '@/constants/tableHeaders';
import { LABELS, VISIBLE_PAGES } from '@/constants/labels'

// INTERFACES
import { STRINGS, SNR}         from '@/interfaces/SmartLink/KPI';
import { HEADER }    from '@/interfaces/SmartLink/Tables'
import { LEGEND }     from '@/interfaces/SmartLink/General';

//FUNCTIONS 
import { getFinalDate } from '@/functions/project'
import { getAllKPICostTableData, getAllKPITableData } from '@/services/Smartlink/KPI';


const { t }                         = useI18n();
const store                         = useStore();
const { smAndDown }                 = useDisplay();

/**
 * Busca en la STORE el valor almacenado de la vist de KPI seleccionado en el DrawerBar
 * y lo almacena en la variable.
 */
const getValueStoreKPI:ComputedRef<string>              = computed (()=>{
    return store.state.valueKPI
});


/**
 * Captura de la Constante el tipo de Leyenda que tiene, dependiendo del valor de la vista seleccionada
 */
const legend:ComputedRef<LEGEND[]>  = computed(() => FINAL_TYPE_LEGEND[getValueStoreKPI.value]);

/**
 * Valor de la Legenda Seleccionada, solo que no se realiza aquí la petición por errores en el Front.
 * Una vez ya se hace el pedido a la API, recién se captura dicha leyenda
 */
const finalLegend     = ref();

/**
 * Captura de la Constante el tipo de Cabecera de Tabla que tiene, dependiendo del valor de la vista seleccionada
 */
const headers:ComputedRef<HEADER[]> = computed(() => FINAL_HEADERS_TABLE[getValueStoreKPI.value]);

/**
 * Valor de los HEADERS Seleccionados, solo que no se realiza aquí la petición por errores en el Front.
 * Una vez ya se hace el pedido a la API, recién se captura dicha cabecera de tabla
 */
const finalHeader:Ref<HEADER[]>     = ref([]); 

/**
 * CREO que sirve para buscar según que columnas
 */
const searchField                   = ref();

/**
 * Busca dentro de la tabla el valor que se escribe aquí
 */
const searchValue                   = ref("");

/**
 * Valor de los ITEMS de los distintos KPI ya filtrados 
 */
const items:Ref<SNR[]>              = ref([]);

/**
 * Muestra el apartado de error, en vez de mostrar información o un espacio vacío. 
 * Generalmente es cuando desde la API no trae valores.
 */
const showError                     = ref(false); 


/**
 * GET ALL DATA
 */
const getDataTable = async () =>{
    showError.value = false;

    items.value         = [];
    finalHeader.value       = headers.value;
    finalLegend.value       = legend.value;
    let params          = { kpi : getValueStoreKPI.value, ip: '' };

    /**
     * Para cuando se trate de KPI de COSTO, se realiza esta lógica
     * Trae datos de las APIs "getCostWiredpeers" & "getCostWirelesspeers"
     */
    if(getValueStoreKPI.value === VISIBLE_PAGES.wiredpeers ||
    getValueStoreKPI.value === VISIBLE_PAGES.wirelesspeers){
        const allData = await getAllKPICostTableData(params)

        if( allData.status === 400 ){
            store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
        }
        if( allData.status === 200 ){
            showError.value = !allData.showGraphs
            items.value = allData.data
        }
    } else {
        const allData = await getAllKPITableData(params)

        if( allData.status === 400 ){
            store.commit('showSnackBar', {type: LABELS.error, msg: t('snackbar.errorApis'), valor : true});
        }
        if( allData.status === 200 ){
            showError.value = !allData.showGraphs
            items.value = allData.data
        }
    }
}


/**
 * Captura el Día actual en formato DATE
 */
const toDay                         = ref(new Date());

/**
 * El día actual lo transforma a número (getTime()) y le resta 30 días.
 * Regresa en forma de número
 */
const thirtyDaysAgo                 = ref(toDay.value.getTime() - (1000 * 60 * 60 * 24 * 30));

/**
 * Se transforma a formato DATE la fecha que fue hace 30 días
 */
const daysAgo                       = ref(new Date(thirtyDaysAgo.value));

/**
 * Llama a la función getFinalDate, enviando la fecha actual, 
 * el cuál regresa la fecha requerida en el formato requerido. 
 */
const finalEndDate                  = getFinalDate(toDay.value);

/**
 * Llama a la función getFinalDate, enviando la fecha de hace 30 días, 
 * el cuál regresa la fecha requerida en el formato requerido. 
 */
const finalStartDate                = getFinalDate(daysAgo.value);

/**
 * Una constante, para captura el valor del título dependiendo del valor de KPI seleccionado
 */
const TITLE_KPI:ComputedRef<STRINGS>=  computed(() => {
    return {
        latency         : t('title.latency'),
        quality_lap     : t('title.quality_lap'),
        quality_snr     : t('title.quality_snr'),
        temp_inst       : t('title.temp_inst'),
        operability     : t('title.operability') + ' ['+finalStartDate.toString() + ' - '+finalEndDate.toString() +'] ',
        wiredpeers      : t('title.wiredpeers'),
        wirelesspeers   : t('title.wirelesspeers'),
    }
});

/**
 * Título dependiendo del valor de la vista seleccionada desde el DrawerBar
 */
const title     = computed(() => TITLE_KPI.value[getValueStoreKPI.value]??'Title');


/**
 * Mostrar el Dialogo
 */
 const dialog        = ref(false);

/**
 * IP del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedIp    = ref('');

/**
 * Nombre del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedName  = ref('');

/**
 * Tipo del equipo Seleccionado de la tabla para mostrar en el DIÁLOGO
 */
const selectedType  = ref('');

/**
 * Captura toda la información del elemento seleccionado en la tabla
 * y se almacenan los valores de (ip, tipo, nombre, marca, estado) en las variables correspondientes
 * para luego abrir el dialog con la información gráfica y estadística del equipo en cuestión
 * @param item La fila seleccionada en la tabla
 */
const showDialog    = (item:SNR) => {
    selectedIp.value    = item.ip??''; 
    selectedType.value  = item.tipo??'';
    selectedName.value  = item.name??'';
    dialog.value        = true;
}

/**
 * Cierra el Diálogo mediante un EMIT del componente de Dialogo
 * @param value valor FALSO
 */
const closeDialog = (value:boolean) => {
    dialog.value = value;
}


/**
 * Cada que se actualiza a una "página distinta"
 * llama de nuevo a la API principal ahora con el nuevo parámetro
 */
watch(getValueStoreKPI,()=>{
    getDataTable()       
})

onMounted(() => {
    getDataTable()
})
</script>

<style>

.table_kpi{
    border: 1px solid rgba(var(--v-theme-colorBorder),0.1)!important;
    border-radius: 20px !important;
    margin: 20px;
}

.btnIpKPI{
    color: rgb(var(--v-theme-colorText));
    /* line-height: 40px; */
    transition: 0.3s;
    font-weight: bold;
}
.btnIpKPI:hover{
    cursor: pointer;
    scale: 1.02;
    transition: 0.3s;
}

</style>