export async function getAllPredictionPREDSNRByIP(params:PARAMETROS){
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_PREDICT.value.getPredSNRByIP,{ params : params, timeout : TIMEOUT })
    .then((response) => {
        const finalData:SNR_PREDICT_GRAPH[] = response.data;
        const finalObj = ref<PREDICT_PRED_SNR>({
            predVER : [],
            predHOR : []
        })
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalData.forEach((item:SNR_PREDICT_GRAPH) =>{
                const itemTime        = new Date(item.fecha??'');
                const milliseconds    = itemTime.getTime();
                const values          = item.snr;
                if(item.causa){
                  if(item.causa.toUpperCase() === LABELS.predsnrh) { finalObj.value.predHOR.push([milliseconds,values]); }
                  if(item.causa.toUpperCase() === LABELS.predsnrv) { finalObj.value.predVER.push([milliseconds,values]); }
                }
            })
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}

export async function getAllPredictionREALSNRByIP(params:PARAMETROS){
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_PREDICT.value.getRealSNRByIP,{ params : params, timeout : TIMEOUT })
    .then((response) => {
        const finalData:EQUIPOS[] = response.data;
        const finalObj = ref<PREDICT_REAL_SNR>({
            realVER : [],
            realHOR : []
        })
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalData.forEach((item:EQUIPOS) =>{
                const itemTime        = new Date(item.fecha??''); 
                const milliseconds    = itemTime.getTime();

                if(projectName.value === 'SmartLinkCananea' || projectName.value === 'SmartLinkDEMO'){
                    const allSNR = JSON.parse(item.snr??`{'H' : 0, 'V' : 0, 'PER' : 0}`);

                    const SNRV        = getArrayOrSingle(allSNR['V'],3);
                    const SNRH        = getArrayOrSingle(allSNR['H'],3);
        
                    finalObj.value.realVER.push([milliseconds,SNRV]);
                    finalObj.value.realHOR.push([milliseconds,SNRH]);
                }
                else{
                    const allSNR      = JSON.parse(item.snr??`{"V": 0, "H":0}`);

                    const SNRV        = getArrayOrSingle(allSNR['V'],0);
                    const SNRH        = getArrayOrSingle(allSNR['H'],0);
        
                    finalObj.value.realVER.push([milliseconds,SNRV]);
                    finalObj.value.realHOR.push([milliseconds,SNRH]);
                    
                }
            })
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}

export async function getAllPredictionTABSNRByIP(params:PARAMETROS){
  /**
   * Estado de la Petición AXIOS
   */
  const statusAxios = ref()
  
  await axios
    .get(APIS_PREDICT.value.getTabSNRByIP,{ params : params, timeout : TIMEOUT })
    .then((response) => {
        const finalData:SNR_PREDICT_TABLE[] = response.data;
        const finalObj = ref<PREDICT_TABLE_SNR_ALL>({
            tabVER : [],
            tabHOR : []
        })
        const showGraphs = ref(finalData.length > 0)
        if(finalData.length > 0){
            finalData.forEach((item:SNR_PREDICT_TABLE) =>{
                const objSNR:OBJSNR = {
                    fecha   : item.fecha,
                    ip      : item.ip,
                    status  : item.status,
                    causa   : item.causa,
                    value   : item.snr,
                }

                if(item.causa.toUpperCase() === 'PREDICT SNR V') { finalObj.value.tabVER.push(objSNR); }
                if(item.causa.toUpperCase() === 'PREDICT SNR H') { finalObj.value.tabHOR.push(objSNR); }
            })
        }
      statusAxios.value = { status: 200, data : finalObj.value, showGraphs : showGraphs.value }
    })
    .catch(function (error) {
      statusAxios.value = { status: 400, data : [], showGraphs : false }
      showConsoleError(error)
    })
  return statusAxios.value
}